@import "src/variables/variables";

.article {

  .article-share {
    margin: 0;

    .container {
      padding: 16px 0;
      border-bottom: 1px solid rgba(40, 41, 41, 0.2);

    }

    &__share-button {
      position: relative;
      cursor: pointer;
      align-items: center;
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 14px;
      line-height: 18px;
      width: max-content;
      color: #0D0D0D;

      .article-share__tooltip {
        cursor: auto;
        position: absolute;
        left: 110%;
        top: 50%;
        transform: translateY(-50%);
        overflow: hidden;
        width: 0;
        transition: width 0.3s ease-in-out;

        &_inner {
          background: rgba(31, 31, 31, 0.5);
          border-radius: 5px;
          padding: 2px 4px;
          color: #fafafa;
          white-space: nowrap;
        }

        &_visible {
          width: 173%;
        }
      }
    }
  }

  .article-main-content {
    font-family: 'IBM Plex Sans', sans-serif;
    margin: 23px 0 66px;

    .container {
      max-width: 780px;
    }

    h2, h3, h4 {
      margin-bottom: 24px;
    }

    h2 {
      font-size: 44px;
    }

    h3 {
      margin-top: 48px;
    }

    h4 {
      font-size: 24px;
      line-height: 130%;
      margin-top: 40px;
    }

    p {
      font-size: 16px;
      line-height: 170%;
      color: #282929;
    }

    ul {
      li {

      }
    }
  }


}