@import "src/variables/variables";

.my-slider {
  width: 100%;
  position: relative;

  &__slide {
    background: #0D0D0D;
    border: 1px solid rgba(255, 255, 255, 0.2);
    padding-bottom: 32px;
  }

  &__inner {
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);

    @include lg {
      flex-direction: column;
    }
  }

  &__img-wrap {
    max-width: 523px;
    width: 100%;
    align-items: flex-end;
    padding-right: 17px;
    border-right: 1px solid rgba(255, 255, 255, 0.2);

    @include lg {
      border-right: none;
      align-self: center;
    }

    img {
      width: 100%;
      object-position: bottom left;
    }
  }

  &__content {
    padding-top: 32px;

    @include lg {
      padding-top: 0;
    }

    &-header, &-text, &-list {
      padding-left: 44px;
      @include lg {
        padding-left: 0;
      }
    }

    &-header {
      border-top: 1px solid rgba(255, 255, 255, 0.2);
      justify-content: space-between;

      @include lg {
        border-top: none;
      }

      &_title {
        padding: 14px 0;
        color: #fff;
        font-family: 'IBM Plex Mono', monospace;
        font-weight: 600;
        font-size: 36px;
        line-height: 130%;

        @include lg {
          padding: 24px 0 14px;
          margin: 0 auto;
        }
        @include small {
          font-weight: 500;
          font-size: 28px;
        }
      }

      &_link {
        width: 192px;
        font-size: 14px;
        line-height: 18px;
        align-items: center;
        justify-content: center;
        border-left: 1px solid rgba(255, 255, 255, 0.2);

        background: linear-gradient(93.67deg, #FFB11B -20.02%, #FF441B 119.31%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;

        @include lg {
          display: none;
        }

        a {
          align-items: center;
          white-space: nowrap;

          svg {
            margin-left: 10px;
            width: 16px;
            height: 16px;
          }
        }

        &_mobile {
          display: none;

          @include lg {
            padding: 14px 0 17px;
            display: flex;
            width: 100%;
          }
        }
      }
    }

    &-text {
      border-top: 1px solid rgba(255, 255, 255, 0.2);
      border-bottom: 1px solid rgba(255, 255, 255, 0.2);

      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 16px;
      line-height: 21px;
      color: rgba(255, 255, 255, 0.5);
      padding-top: 38px;
      padding-bottom: 24px;
      padding-right: 44px;

      @include lg {
        border-top: none;
        border-bottom: none;
        padding: 0 16px;
      }
      @include small {
        font-weight: 500;
        font-size: 14px;
      }

      > p:not(:last-child) {
        margin-bottom: 16px;
      }
    }

    &-list {
      padding-top: 28px;
      padding-bottom: 28px;
      padding-right: 28px;

      @include lg {
        padding: 52px 0 0 0;
      }

      ul {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;

        @include lg {
          gap: 0;
        }

        li {
          min-width: 210px;
          padding: 21px 24px;
          border: 1px solid #282929;

          @include lg {
            flex: auto;
          }

          div {

            &:first-child {
              font-size: 24px;
              line-height: 130%;
              color: #FFFFFF;
              margin-bottom: 8px;

              @include small {
                font-size: 22px;
              }
            }

            &:last-child {
              font-family: 'IBM Plex Sans', sans-serif;
              font-size: 14px;
              line-height: 18px;
              text-transform: uppercase;
              color: rgba(255, 255, 255, 0.3);
            }
          }
        }
      }
    }
  }
}

//MySlider nav

.my-slider {
  &__slide {
    height: 100%;

    > div {
      height: inherit;
    }
  }

  &_nav-arrow {
    position: absolute;
    top: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    background: #0D0D0D;
    border: 1px solid #FFFFFF;
    z-index: 1;
    object-position: center;

    @include small {
      display: none;
    }

    svg {
      transform: rotate(45deg);
    }
  }

  &_prev-arrow {
    left: 0;
    transform: translateX(-50%) rotate(-45deg);

    @include media(1243px) {
      transform: translateX(0) rotate(-45deg);
    }
  }

  &_next-arrow {
    right: 0;
    transform: translateX(50%) rotate(-45deg);

    @include media(1243px) {
      transform: translateX(0) rotate(-45deg);
    }

    svg {
      transform: rotate(225deg);
    }
  }
}