@import "src/variables/variables";

.process-dapps {

  &__cards {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include sm {
      grid-template-columns: 1fr;
    }
    @include small {
      display: none;
    }
  }
}