@import "src/variables/variables";

.latest-news {
  margin-top: 56px;

  h2 {
    color: $light-gray;
    margin-bottom: 32px;
    text-align: center;
  }

  &__list-wrap {
    width: 100%;
    overflow: hidden;
  }

  &__list {
    flex-wrap: nowrap;
    overflow: auto;
    padding-bottom: 8px;
  }
}

.latest-news__list {
  transform: translateX(1px);
}

.latest-news__list::-webkit-scrollbar {
  height: 2px;
}

.latest-news__list::-webkit-scrollbar-track {
  background: rgba(152, 152, 152, 0.4);
}

.latest-news__list::-webkit-scrollbar-thumb {
  background: #1F1F1F;
}