@import "../../variables/variables";

.author {
  align-items: center;

  &__icon {
    width: 40px;
    height: 40px;
  }

  &__name {
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 600;
    font-size: 24px;
    line-height: 130%;
    text-align: start;
    margin-left: 15px;
  }

  &__post {
    font-size: 14px;
    line-height: 18px;
    color: #989898;
  }
}