@import "src/variables/variables";

.ieo-strategy {
  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1px;
    border: 1px solid rgba(40, 41, 41, 0.2);
    background: rgba(40, 41, 41, 0.2);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include small {
      grid-template-columns: repeat(1, 1fr);
    }

    > div {
      background: #FAFAFA;

      &:last-child {
        padding: 24px 24px 26px;
        font-family: 'IBM Plex Sans', sans-serif;

        h3 {
          font-weight: 600;
          font-size: 22px;
          line-height: 130%;
          color: #000000;
        }

        p {
          margin-top: 16px;
          font-size: 16px;
          line-height: 150%;
        }
      }
    }


  }
}