@import 'src/variables/variables';

.mobile-item__opening {
  svg {
    width: 8px;
    height: 6px;
    margin-left: 11px;
    transform-origin: center;
    transform: rotate(0deg);
    transition: transform 0.3s ease-in-out;
  }
}

header {
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  position: fixed;
  z-index: 101;
  background: #080808;

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: 25px 0;

    @include xl {
      width: initial;
    }
  }

  .login-button {
    z-index: 120;
  }
}

.navigation {
  color: white;

  @include lg {
    display: none;
  }
}

.navigation-list {
  display: flex;
  text-transform: uppercase;

  &__link {
    padding: 0 5px;
    font-size: 16px;
    line-height: 21px;
    background: transparent;
    cursor: pointer;
    display: flex;
    align-items: center;

    &:hover:not(:first-child) {
      color: #2f2f2f;
      background: linear-gradient(99.32deg, #FFB11B 6.22%, #FF441B 108.98%);
    }

    &:not(:last-child) {
      margin-right: 28px;
    }

    &:first-child {
      margin-right: 0;
    }

    &_ghost {
      pointer-events: none;
      opacity: 0.4;
      z-index: -1;
    }
  }

  &__link-mobile {
    display: none;
    @include lg {
      display: flex;
    }
  }

  &__link_first {
    display: flex;
    align-items: center;
    padding: 4px 34px;
    position: relative;
    transition: 0s;

    &:hover, &.navigation-list__link_first_active {
      color: #2f2f2f;
      background-color: #fff;

      & svg {
        transform: rotate(180deg);
        margin-left: 11px;
      }

      .navigation-list__drop-down {
        display: block;
      }
    }

    & svg {
      width: 8px;
      height: 6px;
      margin-left: 11px;
      transform-origin: center;
      transform: rotate(0deg);
      transition: transform 0.3s ease-in-out;
    }
  }

  &__drop-down {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 16px 16px;
    background: #fff;
  }

  &__drop-down-item {
    white-space: nowrap;
    font-size: 14px;

    &:hover {
      color: #989898;
    }
  }

  &__drop-down-item:not(:last-child) {
    margin-bottom: 16px;
  }
}

.logo-wrap {
  margin-left: 40px;
  width: 191px;
  height: auto;

  @include lgMiddle {
    margin-left: 23px;
  }
  @include lg {
    width: 180px;
    height: auto;
  }
  @include small {
    margin-left: 12px;
  }

  img {
    width: 100%;
    height: 100%;
  }
}

.order-button-border {
  padding: 13.5px 33.5px;
  outline: none;
  background: transparent;
  border: 1px solid #FFB11B;
  margin-top: 0;
  cursor: pointer;
  font-style: normal;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0.005em;
  text-transform: uppercase;
  font-weight: 900;

  background: linear-gradient(99.32deg, #FFB11B 6.22%, #FF441B 108.98%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;

  &:hover {
    font-weight: 900;
    background: #ffffff;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    border: 1px solid #ffffff;
  }

  @include lg {
    display: none;
  }
}

.mobile-menu {
  display: block;
  position: absolute;
  right: 100%;
  top: 0;
  z-index: 2;
  width: 100%;
  min-height: 100vh;
  height: 100%;
  padding: 64px 0 61px 34px;
  background: #000;
  transition: right 0.3s ease-in-out;
  text-align: left;

  &__inner {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .mobile-navigation-list {
    flex-direction: column;
    text-align: left;
    display: flex;
    gap: 32px;

    &_item {
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 500;
      font-size: 20px;
      line-height: 26px;
      display: flex;
      text-align: center;
      letter-spacing: 0.005em;
      text-transform: uppercase;
      color: #FFFFFF;

      &:last-child {
        margin-bottom: 54px;
      }

      &_opening {
        cursor: pointer;
        align-items: center;
        position: relative;

        svg {
          margin-left: 13px;
        }

        // mobile second dropdown menu
        .second-drop-down-wrapper {
          position: absolute;
          top: -64px;
          left: -150%;
          padding-top: 16px;
          background: #0D0D0D;
          width: 300px;
          min-height: 100vh;
          height: 100%;
          transition: left 0.3s ease-in-out;

          &_opened {
            left: -34px;
          }

          ul {
            li {
              padding: 21px 15px 25px 32px;
              text-align: start;
              font-size: 14px;
              line-height: 18px;
              border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            }
          }
        }
      }
    }
  }

  .order-button-border {
    display: block;
    width: 232px;
  }

  .group-mail {
    div {
      font-weight: 500;
      font-size: 16px;
      line-height: 21px;
      color: #6d7582;
      position: relative;

      &:not(:last-child) {
        margin-bottom: 25px;
      }

      a {
        margin-right: 10px;
      }
    }
  }
}

.show-menu {
  right: 0;
}

.arrow {
  position: relative;
  top: 2.6px;
  height: 16px;
  width: 16px;
}

.hamburger {
  display: none;
  transition: all 0.3s ease-in-out;

  @include lg {
    cursor: pointer;
    display: flex;
    width: 24px;
    height: 24px;
    flex-direction: column;
    right: 26px;
    margin-right: 26px;
    z-index: 3;
  }

  &__line {
    position: relative;
    border-radius: 4px;
    height: 3px;
    width: 26px;
    background: white;
    top: 0;
    right: 0;
    opacity: 1;
    transition: all 0.3s ease-in-out;

    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  &_active {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 50%;

    .hamburger__line {
      width: 16px;
      height: 3px;
      border-radius: 4px;

      &:first-child {
        transform: rotate(-45deg);
        top: 11px;
        right: -4px;
      }

      &:nth-child(2) {
        opacity: 0;
      }

      &:last-child {
        transform: rotate(45deg);
        right: -4px;
        top: -3px;
      }
    }
  }
}