@import "src/variables/variables";

.choose-us-card {
  background: #0D0D0D;

  &__head {
    border-bottom: 1px solid $line-gray;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    pointer-events: none;

    @include small {
      cursor: pointer;
      pointer-events: auto;
    }

    h3 {
      text-align: start;
      margin: 24px 14px 24px 24px;
      font-size: 20px;
      line-height: 26px;
      color: #FFFFFF;
      width: calc(100% - 78px);

      @include small {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }

  &__image-wrap {
    height: 100%;
    width: 78px;
    border-left: 1px solid $line-gray;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__content-wrap {
    overflow: hidden;
    transition: height 0.3s ease-in-out;
  }

  &__paragraph {
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 16px;
    line-height: 21px;
    color: #989898;
    padding: 16px 44px 24px 24px;
    text-align: start;


    @include small {
      font-size: 14px;

    }
  }
}