@import "src/variables/variables";

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include sm {
    padding-bottom: 0;
  }

  &__form-group {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 480px;
    margin-bottom: 20px;
    background: transparent;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;

    @include sm {
      width: 100%;
    }

    &:last-child {
      margin-bottom: 0;
      @include xl {
        align-items: flex-start;
        flex-direction: column-reverse;
      }
      @include mb {
        button {
          width: 100%;
        }
      }
    }

    input, textarea {
      width: 100%;
      height: 56px;
      padding: 13.5px 16px;

      color: white !important;
      border: 1px solid #2f2f2f;
      background: transparent;
      outline: none;

      &:focus,
      &:focus-visible {
        border: 1px solid #FFB11B;
      }

      &:active {
        border: 1px solid #FFB11B;
      }
    }

    textarea {
      height: 104px;

    }

    & .order-button {
      margin-top: 0;
    }
  }

  &__tel-input {
    width: 480px;
    font-weight: 600;
    height: 56px;
    font-size: 14px;
    line-height: 21px;

    @include sm {
      width: 100%;
    }

    .vti__dropdown {
      display: none;
    }
  }

  &__label-file {
    text-transform: uppercase;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    color: #6d7582;

    @include xl {
      display: flex;
      margin-bottom: 32px;
      margin-left: 0;
    }

    svg {
      margin-right: 8px;
    }

    input {
      opacity: 0;
      position: absolute;
      z-index: -1;
      width: 300px;
    }
  }

  &__form-group input:checked + label:after {
    content: "";
    display: block;
    position: absolute;
    top: 2px;
    left: 6px;
    width: 4px;
    height: 8px;
    border: solid #FFB11B;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
    z-index: 5;
  }
}

.form-img-preview {
  max-width: 60px;
  max-height: 60px;

  @include xl {
    margin-bottom: 32px;
  }
}

label {
  cursor: pointer;
}

.label-file {
  margin-left: 158px;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #6d7582;

  @include xl {
    display: flex;
    margin-bottom: 32px;
    margin-left: 0;
  }

  img {
    margin-right: 12px;
  }
}

//Button
.send-button {
  width: 200px;
}