@import "src/variables/variables";

.obama-cards {
  &__grid {
    margin-top: 32px;
    display: grid;
    gap: 1px;
    grid-template-columns: repeat(2, 1fr);
    background: rgba(0, 0, 0, 0.15);
    border: 1px solid rgba(0, 0, 0, 0.15);

    @include sm {
      grid-template-columns: repeat(1, 1fr);
    }
    @include small {
      display: none;
    }
  }

  &__card {
    overflow: hidden;
    padding: 40px 60px 48px 40px;
    background: #FAFAFA;
    position: relative;
    height: 100%;

    @include sm {
      padding: 24px 27px 24px 24px;
    }

    img:not(.obama-cards__card-image) {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 476px;
      height: 496px;


      @include sm {
        max-height: 100%;
        width: auto;
      }
    }

    &-image {
      width: 124px;
      height: 124px;

      @include sm {
        width: 81px;
        height: 81px;
      }
    }

    h3 {
      margin: 66px 0 16px;
      font-size: 28px;
      line-height: 130%;

      @include sm {
        font-size: 24px;
        margin: 32px 0 16px;
      }
    }

    p {
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 500;
      font-size: 16px;
      line-height: 160%;
      color: rgba(10, 14, 27, 0.7);
    }
  }
}