@import "src/variables/variables";

.about-ido {
  margin-bottom: 112px;

  @include sm {
    padding-top: 44px;
    margin-bottom: 35px;
  }

  &__main-wrap {
    margin-top: 112px;

    @include sm {
      margin-top: 56px;
    }
  }

  &__flex {
    margin-top: 40px;
    justify-content: space-between;
    align-items: center;
    gap: 24px;

    @include lgMiddle {
      flex-wrap: wrap;
      justify-content: center;
    }
    @include small {
      gap: 13px;
    }
  }

  &__card {
    padding: 12px;
    align-items: center;
    position: relative;
    background: #FFFFFF;

    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 18px;
    line-height: 180%;
    color: #0D0D0D;

    @include sm {
      flex: auto;
    }
    @include small {
      width: 100%;
    }

    &:after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: linear-gradient(91.49deg, #FFB11B -0.35%, #FF441B 102.07%), #D9D9D9;
    }

    > img {
      width: 40px;
      height: 40px;
      margin-right: 8px;
    }
  }
}