@import "src/variables/variables";

.hover-list {
  padding: 0;
  ul {
    li {
      position: relative;
      display: flex;
      align-items: flex-start;
      padding: 31px 32px;
      border: 1px solid rgba(0, 0, 0, 0.2);

      @include small {
        pointer-events: none;
      }

      @include small {
        padding: 32px 24px;
      }

      &:before {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(91.58deg, #FFB11B -5.07%, #FF441B 106.5%);
        opacity: 0;
        transition: opacity 0.3s ease-in-out;
      }

      &:not(:last-child) {
        border-bottom: none;
      }

      svg {
        width: 0;
        margin-right: 0;
        transition: width 0.3s ease-in-out, margin-right 0.3s ease-in-out;
        transform: translateY(6px);
      }

      .hover-list__content-wrap {
        position: relative;
        z-index: 1;
        max-width: 892px;

        h3, p {
          font-family: 'IBM Plex Sans', sans-serif;
        }

        h3 {
          font-size: 24px;
          margin-bottom: 20px;

          @include small {
            font-size: 22px;
            margin-bottom: 12px;
          }
        }

        p {
          font-size: 16px;
          line-height: 180%;
          color: #3B3B3B;

          @include small {
            font-size: 15px;
          }
        }
      }

      &:hover {
        &:before {
          opacity: 1;
        }

        svg {
          width: 30px;
          margin-right: 42px;
        }
      }
    }
  }
}