@import "src/variables/variables";

.join-block {
  margin-top: 50px;
  margin-bottom: 64px;

  @include lg {
    padding: 50px 16px 64px;
  }

  & .container {
    padding: 24px 0 46px;
    background: #0D0D0D;
    position: relative;
    overflow: hidden;
    text-align: center;

    &:before {
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      max-width: 1053px;
      border-right: 1px solid $line-gray;
      border-left: 1px solid $line-gray;
    }
  }

  h2 {
    margin-bottom: 18px;
    padding-bottom: 24px;
    color: $light-gray;
    border-bottom: 1px solid #2f2f2f;
    width: 100%;

    @include mb {
      padding: 20px 0;
      font-size: 24px;
      line-height: 37px;
    }
  }

  .subtitle {
    margin-bottom: 18px;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    color: #989898;

    @include mb {
      width: 250px;
      margin: 0 auto 21px;
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__form {
    @include small {
      flex-direction: column;

      > div {
        margin-right: 0;
      }

      button {
        margin: 16px auto 0 auto;
        width: 277px;
      }
    }
    @include mb {
      button {
        width: 250px;
      }
    }
  }
}