@import "src/variables/variables";

.crypto-exchange {

  &__grid {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include media(680px) {
      grid-template-columns: repeat(1, 1fr);
    }

    > div {
      &:nth-child(4) {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 2;

        @include media(680px) {
          grid-row-start: auto;
          grid-column-end: auto;
        }
      }
    }
  }
}