@import "src/variables/variables";

.four-boxes {
  flex-wrap: wrap;

  &__box {
    width: 25%;
    padding: 26px 30px;
    border: 1px solid rgba(0, 0, 0, 0.2);

    &:not(:last-child) {
      border-right: none;

      @include small {
        border-right: 1px solid rgba(0, 0, 0, 0.2);
      }
    }

    @include lg {
      width: 50%;

      &:last-child, &:nth-child(2) {
        border-right: 1px solid rgba(0, 0, 0, 0.2);
      }
      &:first-child, &:nth-child(2) {
        border-bottom: none;
      }
    }

    @include small {
      width: 100%;

      &:not(:last-child) {
        border-bottom: none;
      }
    }

    h3 {
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 400;
      font-size: 40px;
      line-height: 110%;
      color: #000000;
      margin-bottom: 15px;
    }

    &_upper {

      h3 {
        font-weight: 500;
        font-size: 24px;
        line-height: 110%;
        text-transform: uppercase;
      }
    }

    div {
      font-size: 14px;
      line-height: 18px;
      display: flex;
      align-items: center;
      text-transform: uppercase;
      color: #000000;
      opacity: 0.7;
    }

    &:first-child {
      background: #000000;

      h3 {
        background: linear-gradient(100.29deg, #FFB11B -4.01%, #FF441B 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
      }

      div {
        color: #FFFFFF;
        opacity: 0.5;
      }
    }
  }
}