@import "src/variables/variables";

.why-choose {

  ul {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1px;
    background: rgba(40, 41, 41, 0.2);
    border: 1px solid rgba(40, 41, 41, 0.2);
    margin-top: 40px;

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }

    @include small {
      grid-template-columns: repeat(1, 1fr);
    }

    li {
      padding: 24px;
      flex: 1;
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 16px;
      line-height: 150%;
      color: #282929;
      background: #FAFAFA;

      img {
        margin-bottom: 16px;
      }

      &:first-child {
        background: #000000;
        color: #FFFFFF;
      }
    }
  }
}