@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
@import "src/variables/variables";

.map__tooltip {
  pointer-events: none;
  position: absolute;
  transition: left 0.3s ease-in-out, top 0.3s ease-in-out, width 0.3s ease-in-out;

  &_moved {
    transform: translate3d(calc(-50% + 6px), -120%, 0);

    @include small {
      transform: translate3d(calc(-50% + 2px), -130%, 0);
    }
  }

  &-inner {
    position: relative;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 25.5271px;
    line-height: 38px;
    background: #FFFFFF;
    padding: 8px 16px;
    display: flex;
    align-items: center;
    white-space: nowrap;

    @include small {
      padding: 4px 8px;
      font-size: 14px;
      line-height: 25px;
    }
    &:after {
      position: absolute;
      content: " ";
      top: 98%;
      left: 50%;
      margin-left: -11px;
      border-width: 11px;
      border-style: solid;
      border-color: #fff transparent transparent transparent;
      z-index: 0;

      @include small {
        top: 96%;

      }
    }

    svg {
      position: absolute;
      top: calc(100% - 57px);
      left: 50%;
      transform: translateX(-50%);
    }
  }
  span {
    margin-left: 9px;
    font-size: 16px;
    line-height: 24px;
    color: #000000;
  }

}