@import "src/variables/variables";

.why-use-ieo {

  &__grid {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1px;
    border: 1px solid rgba(40, 41, 41, 0.2);
    background:  rgba(40, 41, 41, 0.2);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }

    @include small {
      grid-template-columns: repeat(1, 1fr);
    }

    > div {
      background: #FAFAFA;
    }
  }
}