@import "src/variables/variables";

.ido-steps {
  margin-bottom: 65px;

  @include small {
    margin-bottom: 100px;
  }

  &__grid {
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include small {
      grid-template-columns: repeat(1, 1fr);
      display: none;
    }
  }
}