@import 'src/variables/variables';

.smart-transactions-card {
  font-family: 'IBM Plex Sans', sans-serif;
  display: flex;
  justify-content: space-between;
  background: #0D0D0D;
  min-height: 393px;

  &:nth-child(even) {
    flex-direction: row-reverse;
  }

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  &__img-wrap {
    display: block;
    min-width: 389px;
    width: 100%;
    height: auto;

    @include lg {
      min-width: 300px;
    }
    @include sm {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__mobile-img-wrap {
    display: none;
    width: 100%;


    @include sm {
      height: 236px;
      display: block;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }

  }


  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h3, p {
      text-align: start;
    }

    h3 {
      font-size: 44px;
      line-height: 130%;
      color: #fff;
      margin: 12px 0;
      padding-left: 32px;

      @include sm {
        font-size: 22px;
        line-height: 29px;
      }
    }

    p {
      padding: 24px 76px 24px 32px;
      text-align: start;
      font-size: 16px;
      line-height: 24px;
      color: #6D7582;
      border-top: 1px solid $line-gray;
      border-bottom: 1px solid $line-gray;
      height: 100%;
      @include sm {
        font-weight: 500;
        font-size: 14px;
        line-height: 140%;
      }
    }
  }

  &__button {
    margin: 32px auto 32px 32px;

    &:hover {
      border: 1px solid #fff;
    }

    @include sm {
      margin: 32px;
    }
  }
}