@import "src/variables/variables";

.about-smart {
  margin-bottom: 66px;

  .container {
    flex-direction: column;
  }

  &__cards {
    margin-top: 58px;
    justify-content: space-between;
    align-items: stretch;
    gap: 1px;
    background: rgba(40, 41, 41, 0.2);
    border: 1px solid rgba(40, 41, 41, 0.2);

    @include lg {
      flex-direction: column;
      width: 100%;
    }
    @include small {
      margin-top: 40px;
    }
  }

  &__card {
    flex: 1;
  }
}