@import "src/variables/variables";

.article-header {
  position: relative;
  margin: 0;

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(0deg, rgba(31, 31, 31, 0.8), rgba(31, 31, 31, 0.8));
  }

  > img {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  .container {
    padding: 33px 0 76px;
    position: relative;

    h1 {
      margin: 57px 0 24px;
      line-height: 130%;
      font-size: 32px;
      color: #FFFFFF;
    }
  }
}