@import "src/variables/variables";

.media-news-item {
  position: relative;
  padding: 35px 32px 43px 32px;
  border-bottom: 1px solid rgba(40, 41, 41, 0.2);

  @include small {
    padding: 24px 0;
  }

  &:hover {
    .media-news-item__background {
      opacity: 1;

    }
    .date {
      color: #080808;
    }
  }
  .date {
    transition: color 0.3s ease-in-out;
  }
  &__background {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    background: linear-gradient(97.5deg, #FFB11B -2.81%, #FF441B 104.63%);
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  h2 {
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 600;
    font-size: 28px;
    line-height: 130%;
    color: #0D0D0D;
    margin-top: 16px;

    @include small {
      margin-top: 12px;
      font-size: 24px;
    }
  }
}