@import "src/variables/variables";

.consultation {
  padding: 0;
  margin-top: 64px;
  background-color: #0D0D0D;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 176px;
  position: relative;

  @include xl {
    width: calc(100% - 32px);
    padding-bottom: 36px;
    margin: 0 auto;
    height: auto;
    flex-direction: column;
    overflow: hidden;
  }
  @include lg {
    padding-bottom: 66px;
  }
  @include sm {
    margin-top: 77px;
    overflow: visible;
  }

  &::after {
    content: '';
    position: absolute;
    right: 0;
    top: 40px;
    width: 798px;
    height: 1px;
    background-color: #2f2f2f;

    @include xl {
      left: 0;
      right: initial;
      width: 100%;
      top: 270px;
    }
    @include sm {
      top: 178px;
    }
  }

  &::before {
    content: '';
    position: absolute;
    right: 0;
    top: 136px;
    width: 798px;
    height: 1px;
    background-color: #2f2f2f;

    @include xl {
      left: 0;
      right: initial;
      width: 100%;
      top: 360px;
    }
    @include sm {
      top: 120px;
    }
  }

  &__img {
    position: absolute;
    left: 0;
    top: -60px;
    width: 369px;
    height: 272px;
    text-transform: uppercase;
    @include xl {
      position: initial;
      left: initial;
      top: initial;
    }
    @include sm {
      margin-top: -60px;
      width: 232.17px;
      height: 170.78px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__title {
    position: relative;
    margin-left: 430px;
    margin-right: 103px;
    display: flex;
    align-items: center;
    font-size: 32px;
    color: #ffffff;
    height: 95px;

    @include xl {
      margin: 20px 0 54px;
      align-items: flex-start;
    }
    @include sm {
      margin-bottom: 44px;
      font-size: 24px;
      line-height: 37px;
    }

    &::before {
      content: '';
      position: absolute;
      left: -63px;
      top: -41px;
      width: 1px;
      height: 176px;
      background-color: #2f2f2f;
      @include xl {
        top: -292px;
        height: 3000px;
      }
      @include sm {
        top: -131px;
        left: -24px;
        height: 335px;
      }
      @include mb {
        display: none;
      }
    }

    &::after {
      content: '';
      position: absolute;
      right: -103px;
      top: -41px;
      width: 1px;
      height: 176px;
      background-color: #2f2f2f;
      @include xl {
        top: -292px;
        height: 3000px;
      }
      @include sm {
        top: -131px;
        right: -24px;
        height: 335px;
      }
      @include mb {
        display: none;
      }
    }
  }

  &-order {
    position: relative;
    width: 179px;
    height: 95px;
    outline: none;
    margin: 2px 32px 1px 0;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.005em;
    text-transform: uppercase;

    border: 1px solid #FFB11B;
    background: linear-gradient(96.5deg, #FFB11B 7.53%, #FF441B 104.46%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    cursor: pointer;
    transition: border 0.3s ease-in-out;

    &:hover {
      border: 1px solid #fff;
    }

    &:before {
      position: absolute;
      content: '';
      top: calc(50% - 1px);
      right: 0;
      transform: translate3d(2px, -50%, 0);
      height: 176px;
      width: 1px;
      background-color: #2f2f2f;
      @include xl {
        display: none;
      }
    }

    @include lg {
      position: absolute;
      bottom: 20px;
      left: 50%;
      transform: translateX(-50%);
    }

    @include sm {
      bottom: 30px;
    }
  }
}