@import "src/variables/variables";

.media-tabs {

  &__tabs-wrap {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  .media-tab {
    cursor: pointer;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    border: 1px solid #282929;
    transition: background 0.3s ease-in-out;

    &:hover {
      background: rgba(13, 13, 13, 0.1);
    }
    &__inner {
      padding: 16px 56px;

      @include small {
        padding: 12px 32px;
      }
    }

    &_active {
      pointer-events: none;
      background: #0D0D0D;

      &:hover {
        background: #0D0D0D;
      }

      .media-tab__inner {
        background: linear-gradient(95.93deg, #FFB11B -35.7%, #FF441B 147.03%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
      }
    }
  }
}