@import "src/variables/variables";

.nft-create {
  position: relative;
  z-index: 10;

  .container {
    div:first-child {
      h2 {
        max-width: 840px;
      }
    }
  }

  &__grid {
    margin-top: 82px;
    display: grid;
    gap: 1px;
    grid-template-columns: repeat(3, 1fr);
    background: rgba(40, 41, 41, 0.2);
    border: 1px solid rgba(40, 41, 41, 0.2);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }

    @include sm {
      grid-template-columns: repeat(2, 1fr);
    }

    @include small {
      grid-template-columns: repeat(1, 1fr);
      border-left: none;
      border-right: none;
      margin-top: 40px;
    }
  }

  &__card {

    &:first-child {
      grid-column-start: 1;
      grid-column-end: 3;
      background: #080808;

      @include lg {
        grid-column-start: auto;
        grid-column-end: auto;
      }

      h3 {
        color: $light-gray;
      }

      p {
        color: rgba(255, 255, 255, 0.5);
      }

      a div:last-child {
        background: linear-gradient(90.8deg, #FFB11B -1.11%, #FF441B 17.66%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
      }
    }

    &_ghost {
      pointer-events: none;

      h3 {
        opacity: 0.2;
      }

      p {
        color: rgba(0, 0, 0, 0.2);
      }

      .default-card__read-more {
        opacity: 0.2;
      }
    }
  }
}