.modal {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
  background: rgba(31, 31, 31, 0.9);
  width: 100%;
  height: 100%;
  z-index: 1000;
  align-items: center;
  justify-content: center;

  opacity: 0;
  pointer-events: none;

  transition: opacity 0.3s ease-in-out;

  &_opened {
    opacity: 1;
    pointer-events: auto;

    .modal__inner {
      transform: translateY(0) scale(1);
    }
  }

  &__inner {
    transform: translateY(-100%) scale(0.5);
    transition: transform 0.3s ease-in-out;
  }
}