@import "src/variables/variables";

.examples-work {

  @include sm {
    margin-bottom: 100px;
  }

  &__table-wrap {
    @include media(1160px) {
      overflow-y: auto;
    }
  }

  &__table {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-top: 57px;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 16px;
    line-height: 180%;
    color: #282929;
    padding-bottom: 17px;
  }
}

.ex-table {
  @include media(1160px) {
    width: max-content;
  }

  &__cell {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    min-height: 62px;
    background: #FAFAFA;
    border-bottom: 1px solid rgba(40, 41, 41, 0.1);

    &_text {
      font-weight: 600;
      line-height: 21px;
    }

    &:nth-child(3) {
      &:before {
        position: absolute;
        content: '';
        bottom: 100%;
        left: 0;
        width: 100%;
        height: 17px;
        background: #0D0D0D;
      }
    }

    &:nth-child(39) {
      &:before {
        position: absolute;
        content: '';
        top: 100%;
        left: 0;
        width: 100%;
        height: 17px;
        background: #0D0D0D;
      }
    }

    &:nth-child(4n+3) {
      background: #0D0D0D;
      color: #FFFFFF;
      border-bottom: 1px solid #282929;
    }

    &:nth-child(4n+4) {
      border-right: 1px solid rgba(152, 152, 152, 0.4);
    }

    &:nth-child(-2n+4) {
      border-top: 1px solid rgba(152, 152, 152, 0.4);
    }
  }

  &__cell_first {
    justify-content: center;
    align-items: flex-start;
    padding: 16px 40px 8px 0;
    width: 301px;
    border-right: 1px solid rgba(152, 152, 152, 0.4);
  }
}