@import "src/variables/variables";

.about {
  margin-bottom: 0;
  padding-bottom: 105px;

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    &:before {
      position: absolute;
      content: '';
      width: 980px;
      height: 390px;
      border: 1px solid rgba(40, 41, 41, 0.2);
      left: 50%;
      bottom: 27px;
      transform: translateX(-50%);

      @include lg {
        display: none;
      }
    }

    @include mb {
      align-items: center;
      padding: 0 16px;
    }
  }

  &__img {
    max-width: 676px;
    height: auto;
    position: relative;

    @include sm {
      width: calc(100% - 38px);
    }
  }

  &__informations {
    max-width: 780px;
    padding: 50px 115px;
    background: #0D0D0D;
    font-family: 'IBM Plex Sans', sans-serif;

    @include xl {
      width: calc(100% - 38px);
      margin: 0 auto;
      left: initial;
      top: initial;
    }
    @include lg {
      padding: 44px 20px;
    }
    @include mb {
      padding: 24px 18px;
      width: 100%;
    }

    &-title {
      margin-bottom: 12px;
      text-align: start;
      color: $light-gray;

      @include mb {
        border-bottom: 1px solid #2f2f2f;
        padding-bottom: 24px;
        text-align: left;
        font-size: 24px;
        line-height: 37px;
      }
    }

    &-text {
      text-align: left;
      font-weight: 500;
      font-size: 16px;
      line-height: 150%;
      margin-bottom: 24px;
      color: #989898;
      @include sm {
        font-size: 15px;
      }
    }
  }

  &__emails {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    text-align: left;
    gap: 20px;
    position: relative;
  }
}
