@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;500;600;700&family=IBM+Plex+Sans:wght@400;500&family=Titillium+Web:wght@400;600&display=swap');
@import "variables/variables";


@font-face {
  font-family: 'Onest';
  src: url('../public/fonts/Onest-Medium.woff2') format('woff2'),
  url('../public/fonts/Onest-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Onest';
  src: url('../public/fonts/Onest-Regular.woff2') format('woff2'),
  url('../public/fonts/Onest-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Onest';
  src: url('../public/fonts/Onest-ExtraBold.woff2') format('woff2'),
  url('../public/fonts/Onest-ExtraBold.woff') format('woff');
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ul, ol, li {
  padding: 0;
  margin: 0;
  list-style: none;
}

main {
  overflow: hidden;
  padding-top: 100px;

  @include lg {
    padding-top: 82px;
  }
}

section {
  padding: 0 16px;
  margin-bottom: 112px;

  @include sm {
    margin-bottom: 56px;
  }
}

.show {
  z-index: -1;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
  @include xlUp {

  }
}

.stop-scroll {
  overflow: hidden;
}

.flex {
  display: flex;
}

.text-date {
  font-size: 12px;
  line-height: 18px;
  text-transform: uppercase;
  color: #989898;
}

body {
  position: relative;
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 400;
  background: #FAFAFA;
  overflow-x: hidden;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #0D0D0D;
  font-weight: 600;
}

h2 {
  font-weight: 600;
  font-size: 36px;
  line-height: 130%;

  @include sm {
    font-size: 28px;
    line-height: 120%;
  }
}

h3 {
  font-size: 32px;
  line-height: 130%;
}

.description-text {
  font-weight: 500;
  font-size: 36px;
  line-height: 140%;
  text-align: center;
  color: #FFFFFF;

  @include sm {
    font-size: 24px;
  }
}

h5 {
  font-size: 16px;
  line-height: 24px;
}

p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

a {
  color: inherit;
  text-decoration: none;
}

button,
input,
textarea {
  font-family: "Titillium Web", sans-serif, serif;
  font-weight: 600;
  border: none;
}

input, textarea {
  font-size: 14px;

  &::placeholder {
    color: rgba(109, 117, 130, 0.6);
  }
}

//loader
.loader-overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 2000;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.5s ease-in-out;

  &_active {
    opacity: 1;
    pointer-events: auto;

  }
  > div {
    height: 200px;
    width: auto;
  }
}

// SLIDER
.slick-list {
  overflow: hidden;

  @include small {
    overflow: visible;
  }
}

.slick-track {
  display: flex;
}

.slick-track .slick-slide {
  display: flex;
  height: auto;
}

.slick-slide {
  @include small {
    margin: 0 4px;
  }
}

// SLIDER DOTS
.slick-dots {
  display: flex !important;
  justify-content: center;
  align-items: center;
  transform: translateY(-32px);

  ul {
    align-items: center;
    display: flex;
    gap: 10px;
    height: 20px;

    li {
      width: auto;
      height: auto;

      button {
        cursor: pointer;
        width: 8px;
        height: 8px;
        background: #989898;
        border-radius: 8px;
        font-size: 0;
        padding: 0;
        border: none;
        transition: width 0.2s ease-in-out, height 0.2s ease-in-out;

        &:before {
          display: none;
        }
      }

      &.slick-active {
        button {
          width: 10px;
          height: 10px;

          background: #FFFFFF;
        }
      }
    }
  }
}

.default__mobile-slider-wrap {
  margin-top: 40px;
  display: none;

  .slick-dots {
    transform: translateY(6px);

    ul {
      li {
        button {
          background: #989898;
        }

        &.slick-active {
          button {
            background: #0D0D0D;
          }
        }
      }
    }
  }

  @include small {
    display: block;
  }
}