@import "src/variables/variables";

.what-give {

  &__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1px;
    background: rgba(40, 41, 41, 0.2);
    border: 1px solid rgba(40, 41, 41, 0.2);

    @include sm {
      grid-template-columns: repeat(1, 1fr);
    }
    > div {
      border: none;
      background: #FAFAFA;

    }
  }
  &__footer {
    border: 1px solid rgba(40, 41, 41, 0.2);
    padding: 36px 214px;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    transform: translateY(-1px);
    color: #6D7582;
    text-align: center;

    @include lg {
      padding: 36px;
    }
  }
}