@import "src/variables/variables";

.nft-rarity {
  color: #FFFFFF;
  border-top: 1px solid rgba(40, 41, 41, 0.8);
  border-bottom: 1px solid rgba(40, 41, 41, 0.8);

  @include xl {
    padding: 0;
    overflow: hidden;
  }


  &_last {
    transform: translateY(-1px);
    margin-bottom: 9px !important;

    .nft-rarity__title {
      margin-top: 40px;
      border-top: 1px solid #282929;
    }
  }

  .container {
    display: flex;
    border-left: 1px solid #282929;
    border-right: 1px solid #282929;
    min-height: 580px;

    &_reverse {
      flex-direction: row-reverse;

      & .nft-rarity__main-content {
        border-right: none;
        border-left: 1px solid #282929;
        @include sm {
          border-left: none;
        }
      }
    }

    @include xl {
      min-height: 480px;
    }
    @include sm {
      flex-direction: column-reverse;
    }


    @include xl {
      border: none;
    }

    > div:first-child {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
  }

  //left
  &__main-content {
    border-right: 1px solid #282929;
    font-family: 'IBM Plex Sans', sans-serif;

    @include sm {
      border: none;
    }
  }

  &__title {
    padding: 35px 0 34px 40px;
    font-weight: 400;
    font-size: 32px;
    line-height: 110%;
    color: #FFFFFF;
    border-bottom: 1px solid #282929;

    @include sm {
      display: none;
    }

    &_mobile {
      position: relative;
      display: none;
      padding: 18px 0 18px 40px;
      color: #FFFFFF;
      border-top: 1px solid #282929;
      border-bottom: 1px solid #282929;
      margin-top: 30px;

      &:before, &:after {
        position: absolute;
        content: '';
        bottom: 0;
        width: 1px;
        height: 150%;
        background: #282929;
      }

      &:before {
        left: 16px;
      }

      &:after {
        right: 16px;
      }

      @include sm {
        display: block;
      }
    }

    span {
      font-weight: 700;
    }
  }

  &__desc-wrap {
    padding: 48px 40px 64px 64px;
    font-weight: 400;
    font-size: 22px;
    line-height: 150%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

    @include xl {
      padding: 23px 16px 16px 16px;
    }
    @include sm {
      border: 1px solid #282929;
      border-bottom: none;
      margin: 0 16px;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #282929;
    padding: 26px 2px 25px 2px;
    margin-top: 32px;

    &:hover {
      background: linear-gradient(105.19deg, #FFB11B -10.64%, #FF441B 112.72%);

      div {
        background: linear-gradient(0deg, #000000, #000000);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;

        svg {
          path {
            stroke: #000;
          }
        }
      }
    }

    div {
      font-weight: 500;
      font-size: 16px;
      line-height: 21px;
      background: linear-gradient(93.67deg, #FFB11B -20.02%, #FF441B 119.31%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      svg {
        margin-left: 10px;
      }
    }

  }

  //right
  &__media-wrap {
    flex: 1;
    display: flex;
    justify-content: center;
    overflow: hidden;

    @include sm {
      border-left: 1px solid #282929;
      border-right: 1px solid #282929;
      margin: 0 16px;
    }
  }

  &__video {
    width: 175%;
    height: auto;

    @include sm {
      max-height: 400px;
    }
  }

  & model-viewer {
    height: 544px;
    width: 666px;

    @include small {
      height: 325px;
    }
  }
}