@import "src/variables/variables";

.item-with-icon {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  text-transform: uppercase;
  color: #989898;
  width: calc(50% - 27px);

  @include sm {
    width: 100%;
    font-size: 12px;
  }

  .accordion-content__right_img-wrap {
    align-items: center;
    justify-content: center;
    min-width: 48px;
    min-height: 48px;
    background: #141414;
    margin-right: 16px;

  }
}