@import "src/variables/variables";

.security-token {

  &__items {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include sm {
      grid-template-columns: 1fr;
    }
    @include small {
      display: none;
    }
  }
  &__image-block {
    margin-top: 105px;
  }
}
