@import "src/variables/variables";

.what-results {
  overflow: hidden;
  margin-bottom: 97px;

  @include sm {
    padding: 0;
    margin-bottom: 56px;
  }

  .container {
    flex-direction: column;
    align-items: center;

    > img {
      max-width: 670px;
      width: 100%;
      height: auto;
    }
  }

  &__wrap {
    position: relative;
    max-width: 980px;
    min-height: 406px;
    background: #0D0D0D;
    padding: 20px 0 35px;

    @include sm {
      flex-direction: column;
    }

    :before {
      position: absolute;
      content: '';
      width: 1180px;
      height: 499px;
      left: 50%;
      bottom: 35px;
      transform: translateX(-50%);
      border: 1px solid rgba(40, 41, 41, 0.2);
      z-index: -1;
    }
  }

  &__main-content {
    padding: 39px 45px 24px 64px;
    max-width: 554px;
    border-top: 1px solid #282929;
    border-right: 1px solid #282929;
    border-bottom: 1px solid #282929;

    @include lg {
      padding: 16px 18px 24px;
    }

    h2 {
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 40px;
      color: #fff;

      @include sm {
        font-size: 28px;
      }
    }

    p {
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 16px;
      line-height: 150%;
      color: #989898;
      margin: 15px 0 30px;
    }
  }

  &__list {
    padding: 0 64px 0 40px;
    border-top: 1px solid #282929;
    border-bottom: 1px solid #282929;
    align-items: center;

    @include lg {
      padding: 24px 16px;
    }

    ul {
      li {
        display: flex;
        align-items: center;
        font-family: 'Titillium Web', sans-serif;
        font-weight: 600;
        font-size: 14px;
        line-height: 21px;
        text-transform: uppercase;
        color: #989898;
        margin-bottom: 20px;
        min-width: 214px;

        :last-child {
          margin-bottom: 0;
        }

        svg {
          min-width: 24px;
          min-height: 24px;
          margin-right: 16px;

          path {
            fill: #989898;
          }
        }
      }
    }
  }

  &__emails {
    display: block;
    @include sm {
      display: none;
    }
  }

  &__emails_mobile {
    display: none;
    @include sm {
      display: block;

      margin: 19px 0 0 15px;
    }
  }
}