@import "src/variables/variables";

.service {
  position: relative;
  overflow: hidden;
  background: #0D0D0D;
  text-align: left;
  transition: background 0.3s ease-in-out;
  cursor: pointer;

  @include sm {
    padding: 20px 0 0 20px;
  }
  @include small {
    padding: 0;
  }

  > a {
    width: 100%;
  }

  &:first-child {
    .service__img--mb {
      transform: translateY(50px);
    }
  }

  &:nth-child(2) {
    .service__img--mb {
      transform: translateY(0px);
    }
  }

  &:nth-child(3) {
    .service__img--mb {
      transform: translateY(-10px);
    }
  }

  &:nth-child(4) {
    .service__img--mb {
      transform: translateY(0);
    }
  }

  &:nth-child(5) {
    .service__img--mb {
      transform: translateY(0);
    }
  }

  &:nth-child(6) {
    .service__img--mb {
      transform: translateY(0);
    }
  }

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  &:hover {
    background: linear-gradient(99.32deg, #FFB11B 6.22%, #FF441B 108.98%);

    .service__title,
    .service-informations__text {
      color: black;
    }

    .service__link {
      border-color: transparent;
    }

    .service-informations__tag {
      background: #1F1F1F;
      color: white;
    }

    .service__img img {
      opacity: 1;
    }
  }

  &-top__wrap {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-right: 16px;

    &::after {
      content: "";
      position: absolute;
      left: -32px;
      bottom: 0;
      width: calc(100% + 32px);
      height: 1px;
      background: #2f2f2f;
    }

    @include mb {
      padding-right: 12px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 46px;
    height: 46px;
    min-width: 46px;
    min-height: 46px;
    border: 1px solid #ffffff;
    margin-top: 18px;
    border-radius: 50%;
    background: #1F1F1F;

    @include mb {
      position: absolute;
      right: 12px;
      top: 0;
    }
  }

  &__title {
    position: relative;
    font-weight: 600;
    font-size: 32px;
    line-height: 130%;
    color: #ffffff;
    padding: 22px 297px 21px 32px;
    z-index: 1;

    @include sm {
      font-size: 22px;
      padding-right: 0;
    }
    @include mb {
      width: 226px;

      &:after {
        position: absolute;
        content: '';
        width: 1px;
        height: 150%;
        right: 0;
        bottom: 0;
        background: #2f2f2f;
      }
    }
  }

  &-informations {
    display: flex;
    justify-content: space-between;
    padding: 0 78px 0 0;
    position: relative;

    @include sm {
      padding: 0;
    }

    &__wrap {
      padding: 16px 8px 18px 32px;
    }

    &__text {
      width: 100%;
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 16px;
      line-height: 21px;
      color: rgba(225, 225, 225, 0.5);

      @include sm {
        margin-bottom: 54px;
        padding-top: 8px;
        width: 195px;
      }
    }

    &__tag-wrap {
      margin-top: 18px;
      gap: 8px;
      font-weight: 600;
      font-size: 12px;
      line-height: 18px;
      text-transform: uppercase;
      color: #ffffff;
      flex-wrap: wrap;
    }

    &__tag {
      width: max-content;
      padding: 4px 21px;
      background: rgba(109, 117, 130, 0.4);

      @include sm {
      }
    }

    &__wrap {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
  }

  &__img {
    position: relative;
    width: 277px;
    min-width: 277px;
    height: 124px;
    border-radius: 0;

    @include sm {
      height: auto;
      object-fit: contain;
    }

    &--ds {
      @include mb {
        display: none;
      }
    }

    &--mb {
      position: absolute;
      left: 0;
      bottom: 0;
      @include mb {
        display: block;
      }
    }

    img {
      width: 277px;
      height: auto;
      opacity: 0;
      transform: translate(0, 24px);
      transition: opacity 0.2s ease-in-out;

      @include sm {
        width: 220px;
        opacity: 1;
      }
      @include mb {
        width: 160px;
        transform: translate(0, 5px);
      }
    }

    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -50px;
      width: 1px;
      height: 600px;
      background: #2f2f2f;

      @include sm {
        top: -0;
      }
      @include mb {
        display: none;
      }
    }

    &:before {
      content: "";
      position: absolute;
      right: 0;
      bottom: -50px;
      width: 1px;
      height: 600px;
      background: #2f2f2f;

      @include sm {
        display: none;
      }
    }
  }
}