@import "src/variables/variables";

.customer-button {
  cursor: pointer;
  background: linear-gradient(91.96deg, #FFB11B 7.33%, #FF441B 161.84%);
  padding: 15px 26px;
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;

  font-family: 'IBM Plex Sans', sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.005em;
  text-transform: uppercase;
  color: #0D0D0D;

  @include mb {
    width: 100%;
  }

  img {
    transition: transform 0.3s ease-in-out;
  }

  &:hover {
    background: linear-gradient(-91.96deg, #FFB11B 7.33%, #FF441B 161.84%);
    img {
      transform: translateX(25%);
    }
  }
}