@import "src/variables/variables";

.why-semi {
  &__items {
    display: grid;
    gap: 1px;
    grid-template-columns: repeat(4, 1fr);
    border: 1px solid rgba(40, 41, 41, 0.2);
    background: rgba(40, 41, 41, 0.2);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include sm {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  .why-semi-item {
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 16px;
    line-height: 150%;
    color: #282929;
    background: #FAFAFA;
    padding: 24px;


    &__header {
      display: flex;
      align-items: center;
    }

    &__image-wrap {
      width: 40px;
      height: 40px;
      margin-right: 16px;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 500;
      font-size: 22px;

      img {
        position: absolute;
        width: 40px;
        height: 40px;
      }
    }

    h3 {
      font-size: 22px;
      line-height: 130%;
      color: #000000;
    }

    p {
      margin-top: 16px;

    }

    &_dark {
      background: #000000;

      .why-semi-item__image-wrap, p {
        color: #FFFFFF;
      }

      h3 {
        color: #F5F5F5;
      }

    }
  }
}