@import "src/variables/variables";

.partners {
  position: relative;

  @include sm {
    padding-bottom: 40px;
  }

  &::before {
    content: "";
    width: 1053px;
    height: 700px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -371px;
    border: 1px solid rgba(47, 47, 47, 0.2);

    @include lg {
      display: none;
    }
  }

  h2 {
    margin-bottom: 43px;
    text-align: center;
    color: $light-gray;
  }
}

.partners__list {
  max-width: 950px;
  position: relative;
  z-index: 20;
  gap: 20px;
  display: flex;
  flex-wrap: wrap;

  &-item {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin: 0 auto;


    &:not(:last-child) {
      width: 240px;
      height: 100px;
    }

    @include sm {
      width: 240px;
      height: 100px;
    }

    @include mb {
      margin-bottom: 0;
      padding-bottom: 0;
    }

    img {
      width: 100%;
    }

    .partners--hover {
      display: none;
    }

    &:hover {
      .partners--not-hover {
        display: none;
      }

      .partners--hover {
        display: block;
      }
    }
  }
}