@import 'src/variables/variables';

.login-button {
  position: relative;
  z-index: 103;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 25px;
  border: 1px solid #282929;
  color: #282929;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.005em;
  text-transform: uppercase;
  transition: border 0.3s ease-in-out;

  @include lg {
    display: none;
  }

  &:hover {
    border: 1px solid #fff;
  }

  span {
    background: linear-gradient(97.18deg, #FFB11B 7.21%, #FF441B 145.07%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;

    &:first-child {
      margin-right: 6px;
    }

    &:last-child{
      margin-left: 6px;
    }
  }
}