@import "src/variables/variables";

.first-screen-view {
  background: #fff;
  padding: 0;
  margin-bottom: 80px;

  .banner-wrap {
    background: #080808;
    padding-top: 60px;
    position: relative;

    @include xl {
      padding-top: 0;
    }
  }

  .banner {
    position: relative;
    display: flex;

    &::before {
      position: absolute;
      content: '';
      width: 3000%;
      height: 1px;
      top: 0;
      background-color: #2f2f2f;
      left: -50%;
      transform: translateX(-50%);

      @include lg {
        display: none;
      }
    }

    @include xl {
      flex-direction: column;
    }

    &__img {
      position: relative;
      width: 379px;
      min-height: 320px;
      margin: 0 auto;

      @include xl {
        padding: 100px 0 84px;
        //min-height: 224px;
        //width: auto;
      }

      @include mb {
        width: 100%;
        height: auto;
      }

      &::before {
        content: '';
        position: absolute;
        left: -100px;
        top: 50%;
        height: 1px;
        width: 3000%;
        background: #2f2f2f;

        @include xl {
          left: -200%;
        }
      }

      &::after {
        content: '';
        position: absolute;
        left: 50%;
        top: -100px;
        width: 1px;
        height: 520px;
        background: #2f2f2f;
        @include xl {
          height: 1000%;
        }
      }

      img {
        position: relative;
        z-index: 3;
        width: 100%;
        height: 100%;
        transform: translateX(-1px);
      }
    }

    .description {
      border-top: 1px solid #2f2f2f;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 601px;
      height: calc(100% - 87px);
      font-style: normal;
      font-weight: 600;
      font-size: 40px;
      line-height: 61px;
      text-align: center;
      color: #ffffff;

      @include xl {
        margin: 0 auto;
        width: 500px;
        border-top: none;
      }

      @include sm {
        width: initial;
        padding: 40px 18px 44px;
        font-size: 26px;
        line-height: 40px;
      }
    }
  }

  .left {
    height: 520px;
    border-right: 1px solid #2f2f2f;
    position: relative;

    @include xl {
      height: auto;
      display: flex;
      border-right: none;
      padding: 58px 0 46px;
    }

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 1px;
      height: 1000px;
      background: #2f2f2f;
      z-index: 110;

      @include xl {
        display: none;
      }
    }

    &__desktop-social {
      @include xl {
        display: none;
      }
    }

    .description .description-text {
      margin-bottom: 51px;
    }
  }

  .right {
    border-bottom: 1px solid #2f2f2f;
    border-top: 1px solid #2f2f2f;
    width: 100%;
    position: relative;
    display: flex;
    padding: 100px 0;

    @include xl {
      display: flex;
      overflow: hidden;
      padding: 0;
    }
    @include sm {
    }

    &__mobile-socials {
      background: #080808;
      z-index: 1;
      border: none;
      border-right: 1px solid #2F2F2F;
      //justify-content: space-between;
      //height: 100%;

      @include xlUp {
        display: none;
      }
    }


    &::after {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: 1px;
      height: 1000px;
      background: #2f2f2f;
      z-index: 110;
      @include xl {
        height: 2000px;
      }
    }
  }
}