@import "src/variables/variables";

.most-profitable {
  &__grid {
    display: grid;
    gap: 1px;
    grid-template-columns: repeat(4, 1fr);
    border: 1px solid rgba(40, 41, 41, 0.2);
    background: rgba(40, 41, 41, 0.2);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include small {
      grid-template-columns: repeat(1, 1fr);
    }

    > div {
      padding: 24px 21px 33px 24px;
      background: #FAFAFA;
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 500;
      font-size: 16px;
      line-height: 130%;
      color: #000000;

      &:first-child {
        background: #000000;
        color: #F5F5F5;
      }
    }
  }
}