@import "src/variables/variables";

.email-elem {
  display: flex;
  align-items: center;
}

.email {
  font-weight: 800;
  font-size: 13px;
  line-height: 20px;
  margin-right: 13px;
  background: linear-gradient(99.32deg, #FFB11B 6.22%, #FF441B 108.98%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;

  &-desc {
    margin-left: 18px;
    font-size: 14px;
    line-height: 18px;
    color: #989898;

    @include sm {
      font-size: 13px;
      line-height: 17px;
    }

    svg {
      width: 16px;
      height: 16px;
    }
  }
}
