@import "src/variables/variables";

.step-card {
  padding: 40px 24px 24px 24px;
  border: 1px solid rgba(40, 41, 41, 0.2);
  font-family: 'IBM Plex Sans', sans-serif;
  line-height: 130%;
  color: #0D0D0D;
  display: flex;
  flex-direction: column;
  height: 100%;

  img {
    align-self: center;
    margin-bottom: 38px;
  }

  div {
    font-size: 22px;
    opacity: 0.15;
  }

  h3 {
    margin: 12px 0 8px;
    font-size: 28px;
  }

  p {
    font-size: 16px;
    line-height: 150%;
    color: #282929;
  }
}