@import "src/variables/variables";

.media-dev-card {
  justify-content: space-between;
  gap: 16px;
  padding: 32px 0 32px;

  &:not(:last-child) {
    border-bottom: 1px solid rgba(152, 152, 152, 0.2);
  }

  @include lg {
    flex-direction: column-reverse;
  };

  @include small {
    border: 1px solid rgba(152, 152, 152, 0.2);
    padding: 20px 15px;
  };



  &__content {
    max-width: 480px;

    &_inner {
      margin: 16px 0 32px;
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 16px;
      line-height: 170%;
      color: #282929;
      max-width: 480px;

      h3 {
        margin-bottom: 8px;
      }
    }
  }

  &__image-wrap {
    max-width: 580px;

    img {
      width: 100%;
      height: auto;
    }
  }
}