@import "src/variables/variables";

.create-all {

  &__items {
    display: grid;
    gap: 1px;
    grid-template-columns: repeat(2, 1fr);
    background: rgba(40, 41, 41, 0.2);
    border: 1px solid rgba(40, 41, 41, 0.2);
    margin-top: 40px;

    @include sm {
      grid-template-columns: repeat(1, 1fr);
    }

    li {
      padding: 32px 82px 42px 32px;
      flex: 1;
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 16px;
      line-height: 150%;
      color: #282929;
      background: #FAFAFA;

      @include small {
        padding: 32px 24px;
      }

      h3 {
        margin: 24px 0 8px;
        font-size: 20px;
        line-height: 150%;
      }

      &:first-child {
        background: #000000;
        color: #FFFFFF;

        h3 {
          color: #FFFFFF;
        }
      }
    }
  }
}