

.cell-head {
  align-items: center;
  flex-direction: column;
  padding: 25px 21px 31px;
  font-weight: 500;

  h3 {
    font-size: 26px;
    line-height: 130%;
  }

  &__price {
    font-size: 40px;
    line-height: 110%;
    margin: 18px 0 33px;
  }

  &__button {
    cursor: pointer;
    border: 1px solid #0D0D0D;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.005em;
    text-transform: uppercase;
    color: #0D0D0D;
    padding: 10px 20px;
  }

  &_dark {
    color: #FFFFFF;
    width: 100%;
    height: 100%;

    h3 {
      color: #FFFFFF;
    }

    .cell-head__button {
      border: none;
      background: linear-gradient(96.08deg, #FFB11B 7.73%, #FF441B 103.76%);
    }
  }
}