@import "src/variables/variables";

.meta-platform {
  background: radial-gradient(145.22% 107.86% at 90.91% 99.87%, #C9F731 0%, #35CA94 100%);
  overflow: hidden;

  &__container {
    display: flex;
    justify-content: space-between;
    position: relative;

    @include sm {
      flex-direction: column;
    }
  }

  &__logo {
    position: relative;
  }

  &__background-img {
    position: absolute;
    right: -113px;
    top: 50%;
    transform: translate(-33px, -498px) rotate(360deg);

    @include lg {
      width: 700px;
    }
    @include sm {
      width: 600px;
      right: 50%;
      top: 80%;
      transform: translate(50%, -50%) rotate(-3.64deg);
    }
    @include mb {
      width: 500px;

    }
  }

  &__content {
    font-family: 'Onest', sans-serif;
    flex-direction: column;
    align-items: flex-start;
    text-align: start;
    margin: 83px 0 83px;
    @include mb {
      margin: 40px 0;
    }
  }

  &__title {
    font-weight: 800;
    font-size: 54px;
    line-height: 62px;
    letter-spacing: -0.01em;
    color: #041527;
    margin: 64px 0 16px;
    position: relative;

    @include sm {
      font-size: 38px;
      line-height: 48px;
      margin: 40px 0 15px;
    }
  }

  &__paragraph {
    font-size: 16px;
    line-height: 24px;
    color: #282929;
    position: relative;
  }

  &__button {
    font-family: 'Onest', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    justify-content: center;
    align-items: center;
    background: radial-gradient(154.09% 114.47% at 104.5% 102.98%, #775BFF 0.01%, #2948FF 100%);
    border-radius: 8px;
    padding: 16px 59px;
    margin-top: 40px;
    position: relative;

    &:hover {
      background: -o-radial-gradient(104.5% 102.98%, 154.09% 114.47%, #A593FF 0.01%, #3038FF 100%);
      background: radial-gradient(154.09% 114.47% at 104.5% 102.98%, #A593FF 0.01%, #3038FF 100%);
    }

    &:active {
      background: -o-radial-gradient(104.5% 102.98%, 154.09% 114.47%, #6D50FF 22.4%, #2A31DB 100%);
      background: radial-gradient(154.09% 114.47% at 104.5% 102.98%, #6D50FF 22.4%, #2A31DB 100%);
    }

    & svg {
      margin-left: 8px;
    }
  }

  &__main-img {
    position: relative;

    @include lg {
      width: 500px;
    }
    @include sm {
      width: 100%;
    }
  }

  &_project {
    .container {
      border-left: 1px solid #FFFFFF;
      border-right: 1px solid #FFFFFF;
      padding-left: 40px;
      @include xl {
        border: none;
      }
      @include sm {
        padding: 0 16px;
      }
    }

    .meta-platform {
      &__paragraph {
        font-size: 22px;
        line-height: 150%;
        width: 487px;
        @include lg {
          width: 325px;
        }
        @include sm {
          width: 100%;
        }
      }

      &__logo {
        height: 58px;
        margin-bottom: 47px;

        @include sm {
          height: 32px;
        }
      }

      &__button {
        border-radius: 0;
      }

      &__main-img {
        width: 100%;
        max-width: 700px;

        @include xl {
          max-width: 500px;
        }
        @include lg {
          max-width: 380px;
        }
        @include sm {
          max-width: 100%;
        }
      }
    }

  }
}