@import "src/variables/variables";

.three-steps {

  .container {
    h2 {
      margin-bottom: 40px;
    }
  }



  &__flex {
    justify-content: space-between;
    gap: 20px;
    @include sm {
      flex-direction: column;
    }
  }

  &__card {
    padding: 24px;
    border: 1px solid rgba(40, 41, 41, 0.2);
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: #6D7582;
    flex: 1;

    img {
      min-width: 48px;
      min-height: 48px;
    }

    h3 {
      margin: 10px 0 12px;
    }

    p {
      color: #6D7582;
    }
  }
}