@import "src/variables/variables";

.first-screen {
  margin-top: -1px;
  background: #080808;
  border-top: 1px solid $line-gray;
  border-bottom: 1px solid $line-gray;
  margin-bottom: 80px;

  @include xl {
    padding: 0;
  }

  .banner {
    position: relative;
    display: flex;

    @include xl {
      flex-direction: column;
    }

    &::before {
      position: absolute;
      content: "";
      width: 3000%;
      height: 1px;
      top: 0;
      background-color: #2f2f2f;
      left: -50%;
      transform: translateX(-50%);
    }

    .description {
      border-top: 1px solid #2f2f2f;
      padding: 48px 18px 44px;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 601px;
      text-align: center;
      color: #ffffff;
      height: 100%;
      justify-content: center;

      @include xl {
        margin: 0 auto;
      }

      @include sm {
        width: initial;
        padding: 40px 18px 44px;

      }

      &__button {
        margin-top: 64px;
      }

      &-text {
        font-weight: 500;
        font-size: 25.6123px;
        line-height: 156%;
        max-width: 499px;

        @include sm {
          width: initial;
          font-size: 26px;
          line-height: 40px;
        }
      }
    }
  }

  .left {
    border-right: 1px solid #2f2f2f;
    position: relative;

    flex-direction: column;
    justify-content: space-between;

    @include xl {
      border-right: none;
    }

    .list {
      border-top: 1px solid #282929;
      @include xl {
        display: none;
      }
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: -220px;
      width: 1px;
      height: 740px;
      background: #2f2f2f;
      z-index: 102;

      @include xl {
        height: 2000px;
        display: none;
      }
    }
  }

  .right {
    position: relative;

    @include xl {
      display: flex;
    }

    .list {
      @include xlUp {
        display: none;
      }
      @include xl {
        border-bottom: none;
        border-left: none;
      }
    }

    &:after {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      width: 1px;
      height: 740px;
      background: #2f2f2f;
      z-index: 102;

      @include xl {
        height: 2000px;
        display: none;
      }
    }
  }

  .cards {
    display: flex;
    flex-wrap: wrap;

    @include xl {
      width: 100%;
    }

    &-item {
      width: 50%;
      padding-bottom: 114.5px;

      @include xl {
        width: 100%;
        padding-bottom: 60px;
      }

      @include sm {
        padding-bottom: 24px;
      }

      &:first-child {
        border-bottom: 1px solid #2f2f2f;
        border-right: 1px solid #2f2f2f;
        @include xl {
          border-top: 1px solid #2f2f2f;
        }
      }

      &:nth-child(2) {
        border-bottom: 1px solid #2f2f2f;
      }

      &:nth-child(3) {
        border-right: 1px solid #2f2f2f;
        @include xl {
          border-bottom: 1px solid #2f2f2f;
        }
      }

      &:last-child {
        position: relative;

        &::before {
          content: "";
          position: absolute;
          right: -3000px;
          top: -1px;

          height: 1px;
          width: 3000px;
          background: #2f2f2f;
          @include mb {
            display: none;
          }
        }
      }

      &__value {
        width: max-content;
        padding: 21px 20.5px 22px;
        margin-bottom: 12px;

        font-family: 'IBM Plex Sans', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 40px;
        line-height: 110%;

        background: linear-gradient(99.88deg, #FFB11B 0%, #FF441B 196.48%), #FFFFFF;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;

        border-right: 1px solid #2f2f2f;
        border-bottom: 1px solid #2f2f2f;
        color: #ffffff;

        @include sm {
          margin-bottom: 8px;
          padding: 24px 0 0 20px;
          border: none;
          font-size: 36px;
          line-height: 32px;
        }
      }

      &__description {
        margin-left: 12px;
        display: flex;
        text-align: left;
        width: 162px;
        font-style: normal;
        font-size: 14px;
        line-height: 18px;
        text-transform: uppercase;
        color: #989898;

        @include sm {
          width: 100%;
          margin: 0 20px;
          font-size: 12px;
          line-height: 16px;
        }
      }
    }
  }
}