@import "src/variables/variables";

.list {
  display: flex;
  padding: 23px 40px 24px 40px;
  justify-content: space-between;

  @include xl {
    padding-right: 28px;
    padding-left: 28px;
    display: flex;
    flex-direction: column;
    border: 1px solid #2f2f2f;
  }

  &-item {
    display: flex;

    svg {
      min-width: 24px;
      height: 24px;

      path {
        transition: fill 0.2s ease-in-out;
      }
    }

    &:hover {
      svg {
        path {
          fill: #fff;
        }
      }
    }

    &__link {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      border: 1px solid #2f2f2f;
      padding: 12px;
    }

    &:first-child {
      img {
        width: 16px;
        height: 16px;
      }
    }

    &:nth-child(2) {
      img {
        width: 24px;
        height: 24px;
      }
    }

    &:nth-child(3) {
      img {
        width: 24px;
        height: 24px;
      }
    }

    &:nth-child(4) {
      img {
        width: 16px;
        height: 14px;
      }
    }

    &:nth-child(5) {
      img {
        width: 14px;
        height: 16px;
      }
    }

    &:nth-child(6) {
      img {
        width: 17px;
        height: 14px;
      }
    }

    &:nth-child(7) {
      img {
        width: 19px;
        height: 14px;
      }
    }
  }
}

.list--footer {
  border-top: 1px solid $line-gray;
  border-bottom: 1px solid $line-gray;

  @include xl {
    display: none;
  }

  .list-item {

    &:not(:last-child) {
      margin-right: 33px;
    }
  }
}

.list--footer--xl {
  display: none;

  @include xl {
    display: block;
    .list-item {
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }
  @include sm {
    .list-item {
      &:not(:last-child) {
        margin-right: 0
      }
    }
  }
}