@import "src/variables/variables";

.choose-us {
  margin-top: 80px;

  @include small {
    margin-top: 44px;
  }

  .container {
      h2 {
        text-align: center;
      }
  }

  &__grid {
    margin-top: 46px;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include small {
      grid-template-columns: repeat(1, 1fr);
    }

    .choose-us-card {
      background: #0D0D0D;

      &:nth-child(5) {
        grid-column-start: 2;
        grid-column-end: 4;

        @include lg {
          grid-column-start: auto;
          grid-column-end: auto;
        }
      }
    }
  }
}