@import "src/variables/variables";

.form-group {
  position: relative;
  margin-right: 20px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  z-index: 2;

  @include mb {
    width: 100%;
    margin: 0;
  }

  input {
    width: 277px;
    height: 48px;
    color: white;
    padding: 13.5px 16px;
    border: 1px solid #2f2f2f;
    background: transparent;
    outline: none;
    z-index: 1;

    @include mb {
      width: 250px;
      margin: 0;
    }

    &:focus,
    &:focus-visible {
      border: 1px solid #FFB11B;
    }

    &::placeholder {
      font-weight: 600;
      font-size: 14px;
      line-height: 21px;
      text-transform: uppercase;
      color: rgba(109, 117, 130, 0.6);
    }
  }
}

.join-form {
  display: flex;
  justify-content: center;
  @include mb {
    flex-direction: column;
    align-items: center;
  }

  input {
    color: white;

    &::placeholder {
      color: rgba(109, 117, 130, 0.6);
    }
  }


  &__button {
    cursor: pointer;
    z-index: 1;
    width: 179px;
    height: 48px;
    background: #0D0D0D;
    border: 1px solid #FFB11B;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.005em;
    text-transform: uppercase;

    background: linear-gradient(93.67deg, #FFB11B -20.02%, #FF441B 119.31%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;

    transition: border 0.3s ease-in-out;

    @include mb {
      width: 250px;
      margin-top: 16px;
    }

    &:hover {
      border: 1px solid #fff;
    }
  }
}