@import "src/variables/variables";

.what-ico-marketing {
  &__grid {
    display: grid;
    gap: 1px;
    grid-template-columns: repeat(4, 1fr);
    border: 1px solid rgba(40, 41, 41, 0.2);
    background: rgba(40, 41, 41, 0.2);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }

    @include small {
      grid-template-columns: repeat(1, 1fr);
    }

    > div {
      background: #FAFAFA;

      &:first-child {
        background: #000000;
        div {
        color: #fff;
        }
        h3 {
          color: #fff;
        }
      }
    }
  }
}