@import "src/variables/variables";

.ico-advantages {
  &__grid {
    display: grid;
    gap: 1px;
    grid-template-columns: repeat(3, 1fr);
    border: 1px solid rgba(40, 41, 41, 0.2);
    background: rgba(40, 41, 41, 0.2);

    @include media(832px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @include small {
      grid-template-columns: repeat(1, 1fr);
    }

    > div {
      background: #FAFAFA;

      &:first-child {
        background: #000000;

        p {
          color: #FFFFFF;
        }
      }
    }
  }
}