@import "src/variables/variables";

.card-with-img {
  padding: 24px 24px 0 24px;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid rgba(40, 41, 41, 0.2);

  @include small {
    height: 100%;
  }

  h3 {
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 28px;
    line-height: 130%;
  }

  p {
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 16px;
    line-height: 180%;
    color: #282929;
    margin: 8px 0 34px;
  }

  > img {
    margin: 0 auto;
    max-width: 360px;
    width: 100%;
    height: auto;
  }

  &__button {
    background: linear-gradient(92.36deg, #FFB11B -1.24%, #FF441B 102.59%);
    padding: 13px;
    align-items: center;
    justify-content: center;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 21px;
    letter-spacing: 0.005em;
    text-transform: uppercase;
    color: #0D0D0D;

    > img {
      margin-left: 20px;
      transition: transform 0.3s ease-in-out;
    }

    &:hover {
      background: linear-gradient(-91.96deg, #FFB11B 7.33%, #FF441B 161.84%);

      img {
        transform: translateX(25%);
      }
    }
  }

  &_dark {
    background: #0D0D0D;

    h3 {
      color: #FFFFFF;
    }

    p {
      color: #FFFFFF;
    }
  }
}