@import "src/variables/variables";

.all-steps {

  .container {
    align-items: stretch;

    > div {
      flex: 1;
    }

    @include sm {
      flex-direction: column;
    }
  }

  &__main-content {
    color: #fff;
    background: #0D0D0D;
    padding: 37px 64px;

    @include lgMiddle {
      padding: 24px 18px 24px 32px;
    }

    h2 {
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 600;
      font-size: 40px;
      line-height: 120%;
      color: #fff;

      @include sm {
        font-size: 32px;
      }
    }

    p {
      font-family: 'Titillium Web', sans-serif;
      font-size: 20px;
      line-height: 30px;
      margin: 12px 0 32px;
      max-width: 346px;

      @include sm {
        font-size: 16px;
        margin: 40px 0 16px;
      }
    }

    .all-steps__button {

    }
  }

  &__list {
    padding: 47px 126px 47px 96px;
    border: 1px solid rgba(40, 41, 41, 0.2);
    margin-left: -64px;
    height: inherit;
    align-items: flex-start;
    flex-direction: column;

    @include lgMiddle {
      padding: 26px 35px;
      margin-left: 0;
    }

    h3 {
      margin-bottom: 32px;
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 40px;
      line-height: 120%;

      @include sm {
        font-size: 32px;
      }
    }

    ul {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;

      li {
        font-family: 'Titillium Web', sans-serif;
        font-weight: 600;
        font-size: 14px;
        line-height: 21px;
        text-transform: uppercase;
        color: #282929;
        max-width: 175px;

        display: flex;
        align-items: center;
        margin-bottom: 20px;

        @include small {
          max-width: 100%;
          width: 100%;
        }

        svg {
          min-width: 24px;
          min-height: 24px;
          margin-right: 16px;
        }
      }
    }
  }
}