@import "src/variables/variables";

.creative-marketing {

  &__items {
    display: grid;
    gap: 1px;
    grid-template-columns: repeat(4, 1fr);
    border: 1px solid rgba(40, 41, 41, 0.2);
    background: rgba(40, 41, 41, 0.2);

    @include sm {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mb {
      grid-template-columns: repeat(1, 1fr);
    }

    > div {
      background: #FAFAFA;
      padding: 24px 24px 48px;

      &:first-child {
        background: #000000;

        p {
          color: #FFFFFF;
        }
      }

      img {
        width: 32px;
        height: 32px;
      }

      p {
        font-family: 'IBM Plex Sans', sans-serif;
        font-size: 16px;
        line-height: 150%;
        color: #282929;
        margin-top: 16px;
      }
    }
  }
}