@import "src/variables/variables";

.text-block {

  @include sm {
    margin: 40px 0 56px;
  }

  &__text {
    padding: 43px 0;
    position: relative;
    margin: 0 auto;
    max-width: 780px;
    text-align: center;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 16px;
    line-height: 150%;
    color: #000000;
    opacity: 0.8;

    &:before, &:after {
      position: absolute;
      content: '';
      width: 21.6px;
      height: 3.85px;
      background: linear-gradient(92.78deg, #FFB11B -52.22%, #FF441B 182.57%);
      left: 50%;
      transform: translateX(50%);
    }
    &:before {
      top: 0;
    }
    &:after {
      bottom: 0;
    }
  }
}