@import "src/variables/variables";

.section-header {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 auto;

  @include small {
    margin-left: 0;
    text-align: start;
    align-items: flex-start;
  }

  p {
    margin-top: 33px;
    font-family: 'IBM Plex Sans',sans-serif;
    font-weight: 500;
    line-height: 148.5%;
    color: rgba(10, 14, 27, 0.7);
  }
}