@import "src/variables/variables";

.inner-page-header {
  overflow: hidden;
  position: relative;
  min-height: 286px;
  display: flex;
  align-items: center;
  margin-bottom: 48px;

  @include small {
    margin-bottom: 40px;
  }

  &__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .container {
    height: 100%;
    align-items: center;
    justify-content: center;

    h1 {
      position: relative;
      font-weight: 500;
      font-size: 48px;
      line-height: 140%;
      color: #FFFFFF;
      text-align: center;

      @include small {
        font-size: 24px;
      }
    }
  }
}