@import "src/variables/variables";

.why-blockchain {
  position: relative;
  z-index: 10;

  > div {
    flex-direction: column;
  }

  h2 {
    text-align: center;
    color: #101010;
    margin: 0 auto 32px;

    @include lg {
      width: max-content;
      padding: 0 16px;
    }
  }

  &__subtitle {
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 148.5%;
    margin: 0 auto 72px;
    color: rgba(10, 14, 27, 0.7);

    @include sm {
      padding: 0 16px;
      width: 100%;
    }
  }

  &__grid {
    display: grid;
    gap: 1px;
    grid-template-columns: repeat(4, 1fr);
    background: rgba(40, 41, 41, 0.2);
    border: 1px solid rgba(40, 41, 41, 0.2);

    @include lg {
      grid-template-columns: repeat(3, 1fr);
    }

    @include sm {
      grid-template-columns: repeat(2, 1fr);
    }

    @include small {
      grid-template-columns: repeat(1, 1fr);
      border-left: none;
      border-right: none;
    }
  }
}