@import "src/variables/variables";

.services-crypto {
  margin-bottom: 112px;

  @include sm {
    margin-bottom: 56px;
  }

  &__grid {
    margin-top: 46px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include small {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}