@import "src/variables/variables";

.choosing-gamefi {

  img {
    width: 100%;
    height: auto;

  }

  &__items {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 20px;

    @include small {
      gap: 8px;
      grid-template-columns: repeat(1, 1fr);
      margin-top: 8px;
    }
  }

  &__item {
    padding: 27px 48px 32px 32px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    line-height: 150%;

    @include small {
      padding: 24px;
    }

    &:last-child {
      grid-column-start: 1;
      grid-column-end: 3;

      @include small {
        grid-column-end: 1;
      }
    }

    h4 {
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 20px;
      color: #000000;
      margin-bottom: 8px;
    }

    p {
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 20px;
      color: #000000;

      span {
        font-weight: 700;
      }
    }
  }
}