@import "../../../variables/variables";

.about-block {

  h2 {
    text-align: center;

    > span {
      font-weight: 400;
    }
  }

  &__content-wrap {
    margin-top: 40px;

    &_revers {
      flex-direction: row-reverse;
    }

    @include sm {
      flex-direction: column;
    }

    > div {
      border: 1px solid rgba(0, 0, 0, 0.15);
      padding: 34px 55px 48px 40px;
      flex: 1;

      @include small {
        padding: 24px 24px 42px;
      }

      h3 {
        background: linear-gradient(91.34deg, #FFB11B -9.74%, #FF441B 39.7%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        margin-bottom: 16px;
      }

      p {
        font-family: 'IBM Plex Sans', sans-serif;
        font-weight: 500;
        font-size: 16px;
        line-height: 160%;
        color: #FFFFFF;
        opacity: 0.8;

        > span {
          background: linear-gradient(95.47deg, #FFB11B -4.78%, #FF441B 22.41%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
          text-fill-color: transparent;
          margin-bottom: 16px;
        }
      }

      &:last-child {
        p {
          color: #000000;
          opacity: 0.8;
        }
      }
    }
  }

  &__left {
    background: #080808;
    position: relative;

    @include sm {
      min-height: 264px;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;

    }

  }

  &__right {
    transform: translate(0, 42px);

    > div {
      margin-top: 16px;
      max-width: 240px;

      @include small {
        max-width: 100%;
      }
    }

    @include sm {
      transform: translate(0);
    }
  }
}