@import "../../variables/variables";

.default-card {
  display: flex;
  padding: 24px 16px;
  background: #FAFAFA;

  > img {
    display: none;
    @include small {
      width: 24px;
      height: 24px;
      display: block;
      margin-right: 12px;
    }
  }

  &__link {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    height: 100%;
    font-family: 'IBM Plex Sans', sans-serif;
    line-height: 130%;
    font-size: 16px;
    color: #6D7582;

    &:hover {
      span {
        margin-right: 10px;
      }
    }
  }

  &__inner-wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    > img {
      display: block;

      @include small {
        display: none;
      }
    }

    h3 {
      margin: 10px 0;
      font-size: 32px;
      line-height: 130%;
      text-align: start;

      @include small {
        font-size: 22px;
        margin-top: 0;
      }
    }

    p {
      text-align: start;
      font-size: 16px;
      line-height: 150%;
      color: #6D7582;

      @include small {
        font-weight: 500;
        font-size: 12px;
      }
    }
  }

  &__read-more {
    margin-top: 29px;
    color: #0D0D0D;
    font-weight: 700;
    font-size: 18px;

    @include small {
      font-size: 12px;
    }

    span {
      text-decoration-line: underline;
      transition: margin-right 0.3s ease-in-out;
    }
  }
}
