@import "src/variables/variables";

.can-trust {
  margin-bottom: 20px;

  .container {
    h2 {
      max-width: 587px;
      margin: 0 auto 51px auto;
      text-align: center;
    }
  }


  &__cards {
    align-items: stretch;
    justify-content: space-between;
    gap: 20px;
    @include sm {
      flex-direction: column;
    }
  }
}

.trust-card {
  padding: 24px 24px 0 24px;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid rgba(40, 41, 41, 0.2);

  h3 {
    font-size: 28px;
    line-height: 130%;
  }

  p {
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 16px;
    line-height: 180%;
    color: #282929;
    margin: 8px 0 34px;
  }

  img {
    margin: 0 auto;
    max-width: 360px;
    width: 100%;
    height: auto;
  }
}