@import "src/variables/variables";

.play-to-earn {

  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1px;
    border: 1px solid rgba(40, 41, 41, 0.2);
    background: rgba(40, 41, 41, 0.2);

    @include media(690px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &-item {
    min-height: 160px;
    background: #FAFAFA;
    padding: 24px;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 600;
    font-size: 22px;
    line-height: 130%;
    color: #000000;

    &:first-child {
      background: #000000;
      color: #F5F5F5;

      div {
        color: #FFFFFF;
      }
    }

    &:nth-child(2) {
      grid-column-start: 2;
      grid-column-end: 4;

      @include media(690px) {
        grid-column-start: 1;
        grid-column-end: 2;
      }
    }

    &__number-wrap {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      height: 32px;
      width: 32px;
      margin-bottom: 16px;
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: #000000;

      img {
        position: absolute;
        width: 32px;
        height: 32px;
      }
    }
  }
}