@import "src/variables/variables";

.square-content-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 100px;
  margin-top: 40px;


  &_reverse {
    flex-direction: row-reverse;


    @include sm {
      margin-top: 80px;
      flex-direction: column;
    }

    .square-content-box__image-wrap {

      img {
        transform: translate(-40px, -40px);
      }
    }
  }

  &__image-wrap {
    flex: 1;
    border: 1px solid rgba(0, 0, 0, 0.2);
    max-width: 500px;
    width: 100%;

    @include sm {
      max-width: 300px;
    }
    @include mb {
      max-width: 260px;
    }

    img {
      width: 100%;
      height: auto;
      transform: translate(40px, -40px);

      @include mb {
        transform: translate(20px, -20px);
      }
    }
  }

  &__content-wrap {
    flex: 1;

    div {
      font-weight: 600;
      font-size: 28px;
      line-height: 130%;
      background: linear-gradient(92.4deg, #FFB11B -0.59%, #FF441B 11.29%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      margin-bottom: 16px;
    }

    p {
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 400;
      font-size: 20px;
      line-height: 150%;
      color: #000000;
      opacity: 0.8;
      margin-bottom: 16px;

      @include small {
        font-size: 16px;
      }
    }
  }

  @include sm {
    flex-direction: column;
    gap: 32px;
  }
}