@import "src/variables/variables";

.influencers-finance {
  .container {
    background: #080808;
    padding: 41px 80px 60px;
    margin-top: 41px;

    @include lgMiddle {
      padding: 24px 40px 47px 16px;
    }

    h2 {
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 500;
      font-size: 28px;
      line-height: 130%;
      color: #FFFFFF;
      margin-bottom: 55px;

      @include sm {
        margin-bottom: 27px;
        font-weight: 600;
        font-size: 20px;
        line-height: 26px;
      }
    }

    ul {
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      row-gap: 32px;
      column-gap: 40px;

      li {
        width: calc(33.333% - 30px);

        @include lg {
          width: calc(50% - 30px);
        }

        @include sm {
          width: 100%;
          row-gap: 24px;
        }
      }
    }
  }
}


