@import "src/variables/variables";

.footer {
  padding: 43px 0 0;
  border-top: 1px solid #2F2F2F;
  overflow: hidden;
  position: relative;
  background-color: #0D0D0D;
  font-family: 'IBM Plex Sans', sans-serif;

  @include mb {
    overflow: visible;
    //margin-top: 60px;
  }

  input {
    &::placeholder {
      text-transform: uppercase;
    }
  }

  .left-wrap {
    position: relative;
    width: 601px;
    margin-top: -4px;

    @include xl {
      position: initial;
      width: 100%;
    }

    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: -50%;
      width: 1px;
      height: 400%;
      background: #2f2f2f;

      @include xl {
        display: none;
      }
    }

    &:after {
      content: "";
      position: absolute;
      right: 0;
      top: -50%;
      width: 1px;
      height: 400%;
      background: #2f2f2f;

      @include mb {
        display: none;
      }
    }
  }

  &__tags {
    flex-wrap: wrap;
    display: flex;
    padding: 43px 20px 42px 20px;
    color: white;
    border-top: 1px solid #2f2f2f;

    @include xl {
      display: none;
    }

    &-el {
      margin-bottom: 16px;
      text-align: left;
      font-size: 13px;
      line-height: 17px;
      letter-spacing: 0.005em;
      text-transform: uppercase;
      cursor: pointer;
      width: 182px;
      min-width: 118px;

      &:hover a {
        font-weight: 900;
        background: linear-gradient(99.32deg, #FFB11B 6.22%, #FF441B 108.98%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
      }
    }
  }

  &__img {
    margin: 0 auto;
    width: 600px;
    height: 452px;
    display: flex;
    align-items: flex-end;

    @include xl {
      display: none;
    }

    > img {
      width: 600px;
      height: auto;
    }

    &_mobile {
      display: none;

      > img {
        width: 100%;
        height: 100%;
      }

      @include xl {
        display: block;

      }
    }
  }

  .footer__informations {
    display: flex;
    text-align: left;
    align-items: center;
    padding: 24px 21px 24px 13px;
    color: rgba(109, 117, 130, 0.8);
    border-bottom: 1px solid #2f2f2f;
    border-top: 1px solid #2f2f2f;

    @include xl {
      display: flex;
      justify-content: space-between;
      padding: 0;
    }

    @include sm {
      padding-bottom: 0;
      padding-top: 0;
      position: relative;
    }

    .footer-emails {
      display: none;

      @include sm {
        margin-top: 66px;
        padding: 32px 0;
        display: flex;
        flex-direction: column;
        border-top: 1px solid #282929;

        .footer-email {

          @include sm {
            margin-left: 15px;
          }

          img {
            width: 16px;
            height: 16px;
          }

          &:first-child {
            margin-bottom: 16px;
          }
        }
      }
    }

    &-wrap {
      display: flex;

      @include xl {
        margin-left: 15px;
      }
      @include sm {
        margin-left: 0;
        flex-direction: column;
        width: 100%;
        padding-top: 66px;
        border-right: 1px solid #282929;

        .footer__logo-description {
          margin-top: 20px;
          max-width: 300px;

        }
      }
    }

    // mob socials
    &_mobile-socials {
      display: none;
      border: none;
      gap: 12px;

      @include xl {
        display: flex;
      }
    }
  }

  .footer__logo {
    align-self: flex-start;
    margin-right: 20px;
    width: auto;
    height: 60px;

    @include sm {
      padding-left: 15px;
    }

    img {
      width: 100%;
      height: 100%;
    }

    &-description {
      width: 100%;
      max-width: 300px;
      font-weight: 300;
      font-size: 13px;
      line-height: 17px;
      text-transform: lowercase;
      color: #B6B6B6;

      @include sm {
        padding: 0 15px;
      }
    }
  }

  .container {
    display: flex;

    @include xl {
      flex-direction: column-reverse;
    }
  }
}

.right-wrap {
  position: relative;
  width: 580px;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include sm {
    width: 100%;
    padding: 0 16px;
    margin-bottom: 40px;
  }

  &:after {
    content: "";
    position: absolute;
    right: 0;
    top: -50%;
    width: 1px;
    height: 400%;
    background: #2f2f2f;
    @include xl {
      display: none;
    }
  }
}

.footer-form__title {
  margin-bottom: 32px;
  font-style: normal;
  font-weight: 600;
  font-size: 40px;
  line-height: 130%;
  text-align: center;
  color: #ffffff;

  @include sm {
    font-size: 28px;
    line-height: 43px;
  }
}

.footer-socials {
  padding: 34px 50px;
  border-top: 0.999154px solid #2f2f2f;
  border-bottom: 0.999154px solid #2f2f2f;

  .list {
    .list-item {
      &:not(:last-child) {
        margin-right: 33px;
      }
    }
  }

  @include xl {
    display: none;
  }
}

.footer-emails {
  display: flex;

  .footer-email {
    font-size: 13px;
    line-height: 17px;
    color: #989898;
    transition: color 0.3s ease-in-out;

    &:hover {
      color: #fff;
    }

    img {
      width: 16px;
      height: 16px;
    }

    span {
      margin-right: 10px;
    }

    img {
      position: relative;
      top: 3px;
    }

    &:first-child {
      margin-right: 48px;
    }
  }
}

.footer {
  .container--bottom {
    position: relative;
    z-index: 10;
    padding: 20px 24px 20px 20px;
    justify-content: space-between;

    @include xl {
      padding-top: 20px;
      text-align: center;
      .footer-emails {
        display: none;
      }
    }
    @include mb {
      padding: 12px 0;
    }

    .footer-kwk-wrap {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end;
      text-align: end;
      font-weight: 300;
      font-size: 13px;
      line-height: 17px;
      color: #989898;

      @include xl {
        align-items: center;
        margin-bottom: 16px;
      }
    }
  }
}

.year {
  font-weight: 300;
  font-size: 13px;
  line-height: 17px;
  color: #989898;
  margin-left: 21px;

  @include xl {
    margin-left: 0;

  }
}

