@import "src/variables/variables";

.smart-development {
  padding-top: 47px;
  margin-bottom: 96px;

  .container {
    flex-direction: column;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-top: 40px;

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include small {
      grid-template-columns: repeat(1, 1fr);
      display: none;
    }
  }
}