@import "src/variables/variables";

.communities {
  display: flex;
  flex-direction: column;
  justify-content: center;

  @include xl {
    justify-content: center;
  }
  @include lg {
    padding: 0 20px;
  }
  @include mb {
    padding: 0 16px 40px;
  }

  & > .container {
    flex-direction: column;
    width: 1181px;
    @include xl {
      width: 100%;
    }
  }

  &__title {
    display: flex;
    justify-content: flex-start;
    width: 282px;
    text-align: left;
    color: $light-gray;
    margin-top: 21px;

    @include xl {
      margin-bottom: 30px;
      width: 100%;
      margin-left: 0;
      padding: 0;
    }
    @include mb {
      margin-bottom: 24px;
      margin-top: 30px;
      font-size: 28px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);

    @include xl {
      grid-template-columns: repeat(3, 1fr);
    }
    @include lg {
      flex-direction: column;

    }
    @include media(800px) {
      grid-template-columns: repeat(1, 1fr);
    }

    &--wrap {
      display: flex;
      justify-content: space-between;
      width: 100%;

      @include xl {
        flex-direction: column;
        align-items: center;
      }
      @include lg {
        align-items: stretch;
      }
    }
  }
}

.community {
  min-width: 285px;
  position: relative;
  overflow: hidden;
  cursor: pointer;

  @include xl {
    min-width: 300px;
  }

  @include lg {
    width: 100%;
    min-width: auto;
    border-right: 1px solid rgba(47, 47, 47, 0.2);
    border-bottom: 1px solid rgba(47, 47, 47, 0.2);
  }

  &:first-child {
    pointer-events: none;
    border-right: none;

    h2 {
      justify-content: flex-start;
      @include xl {
        justify-content: center;
      }
    }

    @include xl {
      grid-column-start: 1;
      grid-column-end: 4;
    }

    @include media(800px) {
      grid-column-start: 1;
      grid-column-end: 2;
    }
  }

  &:not(:last-child) {
    .community__wrap--top,
    .community__wrap--bottom {
      border-right: none;
    }
  }

  &__wrap--top,
  &__wrap--bottom {
    justify-content: space-between;
    display: flex;
    align-items: center;
    text-align: left;
    padding: 20px 16px 20px;
    border: 1px solid rgba(47, 47, 47, 0.2);
  }

  &__wrap--bottom {
    @include mb {
      padding: 0 16px 8px 56px;
      border: none;
      border-left: 1px solid rgba(47, 47, 47, 0.2);
      /* border-top: 1px solid rgba(47, 47, 47, 0.2); */
      border-right: 1px solid rgba(47, 47, 47, 0.2);
    }

    .community {
      &:nth-child(1) {
        .community__img {
          @include mb {
            width: 25px;
            height: 19px;
          }
        }
      }

      &:nth-child(2) {
        .community__img {
          @include mb {
            width: 22px;
            height: 18px;
          }
        }
      }

      &:nth-child(3) {
        .community__img {
          @include mb {
            width: 22px;
            height: 22px;
          }
        }
      }
    }
  }

  &__wrap--top {
    border-bottom: none;
    position: relative;
    @include mb {
      position: initial;
      padding: 10px 16px 4px 56px;
    }

    .community {
      &:first-child {
        .community__img {
          @include mb {
            width: 24px;
            height: 18px;
          }
        }
      }

      &:nth-child(2) {
        .community__img {
          @include mb {
            width: 22px;
            height: 18px;
          }
        }
      }

      &:nth-child(3) {
        .community__img {
          @include mb {
            width: 32px;
            height: 32px;
          }
        }
      }
    }

    &::before {
      position: absolute;
      content: "";
      background: rgba(47, 47, 47, 0.2);
      right: 76px;
      top: 0;
      width: 1px;
      height: 300%;
      @include mb {
        display: none;
      }
    }
  }

  &__informations {
    max-width: 185px;
    height: 48px;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 16px;
    line-height: 150%;
    color: #989898;

    @include xl {
      height: 60px;
      max-width: 160px;
      width: max-content;
    }
    @include media(800px) {
      max-width: 215px;
    }
    @include mb {
      font-size: 14px;
      max-width: 191px;
    }
  }

  &__link,
  &__img {
    display: flex;
    padding: 0;
  }

  &__img {
    margin-right: 16px;
    @include mb {
      position: absolute;
      left: 17.33px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__link {
    position: relative;
    display: flex;
    margin-right: 16px;

    @include mb {
      position: absolute;
      top: 50%;
      right: 16px;
      transform: translateY(-50%);
    }

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 44px;
      height: 44px;

      border-radius: 50%;

      background: #101010;
      border: 1px solid #101010;
      @include mb {
        width: 32px;
        height: 32px;
      }
    }

    &::after {
      @include mb {
        content: "";
        position: absolute;
        left: -36px;
        top: -200px;
        width: 1px;
        height: 100vh;
        background: rgba(47, 47, 47, 0.2);
      }
    }

    svg {
      position: relative;
      z-index: 2;
      @include mb {
        height: 10px;
        width: 11px;
      }
    }
  }

  &__title {
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 600;
    font-size: 32px;
    line-height: 130%;
    color: $light-gray;

    @include mb {
      font-weight: 500;
      font-size: 20px;
    }
  }

  &:hover {
    background: linear-gradient(99.32deg, #FFB11B 6.22%, #FF441B 108.98%);
    color: black;

    .community__title {
      color: black;
    }

    .community__informations {
      color: black;
    }
  }
}