@import "../../variables/variables";

.covered {
  .container {
    padding: 0;
    display: flex;
    background: #0D0D0D;
    height: 301px;
    position: relative;

    @include lg {
      height: auto;
    }

    &:before {
      position: absolute;
      content: '';
      top: -56px;
      left: 50%;
      transform: translateX(-50%);
      width: 1052px;
      height: 391px;
      border: 1px solid rgba(0, 0, 0, 0.3);
      z-index: -1;

      @include sm {
        display: none;
      }
    }
  }

  &__img-wrap {
    align-self: flex-end;
    transform: translateY(4px);

    img {

      @include lgMiddle {
        width: 100%;
        height: auto;
      }

      @include lg {
        display: none;
      }
    }
  }

  &__middle-line {
    width: 1px;
    height: 100%;
    background: $line-gray;
  }

  &__content {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    text-align: start;
    flex-direction: column;
    width: 100%;
    padding: 26px 0 26px 56px;
    margin: 28px 0;
    border-top: 1px solid $line-gray;
    border-bottom: 1px solid $line-gray;

    @include sm {
      justify-content: center;
      padding: 26px 26px 26px 56px;
    }
    @include small {
      padding: 22px 16px 24px;
    }

    h2 {
      font-size: 32px;
      color: #fff;

      @include small {
        font-family: 'IBM Plex Sans', sans-serif;
      }
    }

    p {
      font-family: 'Titillium Web', sans-serif;
      font-size: 16px;
      line-height: 24px;
      color: #989898;
      max-width: 509px;
      margin: 8px 0 32px;
    }
  }

  &__form {
    width: max-content;

    @include sm {
      flex-direction: column;
      width: 100%;

      > div {
        margin-right: 0;
      }
      button {
        margin-top: 16px;
      }
      div input, button {
        width: 100%;
      }
    }
  }

  &__button {
    margin-top: 32px;

    @include small {
      width: 100%
    }
  }
}