@import "src/variables/variables";

.modal-form {
  max-width: 1180px;
  background: #0D0D0D;
  position: relative;

  > svg {
    top: 16px;
    left: 16px;
    cursor: pointer;
    position: absolute;
    transition: opacity 0.2s ease-in-out;
    opacity: 0.5;

    &:hover {
      opacity: 1;
    }
  }


  &__right-wrap {
    padding: 41px 80px;
    @include small {
      padding: 50px 16px 18px;
    }

    h2 {
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 600;
      font-size: 40px;
      line-height: 130%;
      text-align: center;
      color: #FFFFFF;
      margin-bottom: 29px;
      @include small {
        font-size: 24px;
        line-height: 31px;
      }
    }
  }
}

.modal-left {
  max-width: 544px;
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  @include xl {
    display: none;
  }

  &__img-wrap {
    display: flex;

    img {
      width: 100%;
    }
  }

  &__informations {
    ul {
      border-top: 1px solid rgba(255, 255, 255, 0.2);
      border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    }

    &-wrap {
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 300;
      font-size: 13px;
      line-height: 17px;
      text-transform: lowercase;
      color: #B6B6B6;
      align-items: center;
      padding: 24px 21px;
    }

    & .modal-left__logo {
      margin-right: 24px;
    }
    & .modal-left__logo-description {
      max-width: 300px;
    }
  }
}