@import "src/variables/variables";

.contact-us {
  font-family: 'IBM Plex Sans', sans-serif;

  @include small {
    margin-bottom: 0;
  }

  .container {
    min-height: 420px;
    border: 1px solid rgba(40, 41, 41, 0.2);
    padding: 30px 0;

    @include sm {
      flex-direction: column;
      border: none;
    }
    @include mb {
      h3 {
        font-size: 28px;
      }
      p {
        padding-top: 16px;
        border-top: 1px solid rgba(0, 0, 0, 0.3);
      }
    }
  }

  p {
    font-size: 16px;
    line-height: 150%;
    color: #989898;
  }

  &__contact {
    padding: 60px 43px 60px 64px;
    background: #0D0D0D;
    align-self: flex-start;
    margin-left: -30px;

    @include sm {
      padding: 24px 18px 36px;
      margin: 0;
    }
    @include mb {
      button {
        width: 100%;
      }
    }

    h3 {
      color: #fff;
    }

    p {
      margin: 8px 0 16px;
    }
  }

  &__additional {
    padding: 73px 73px 73px 96px;
    margin-right: -30px;
    margin-left: -63px;
    align-self: flex-end;
    border: 1px solid rgba(40, 41, 41, 0.2);

    @include sm {
      padding: 24px 18px 26px;
      margin: 0;
    }

    p {
      font-weight: 500;
      margin-top: 10px;
    }
  }
}