@import "src/variables/variables";

.accordion-item {
  background: #080808;

  &__header {
    background: linear-gradient(90.9deg, #FFB11B -0.88%, #FF441B 106.05%);

    &-inner {
      cursor: pointer;
      align-items: center;
      justify-content: space-between;
      min-height: 110px;
      border: 1px solid rgba(40, 41, 41, 0.2);
      background: #080808;
      transition: background 0.2s ease-in-out;

      h2 {
        width: 435px;
        margin-left: 40px;
        font-family: 'IBM Plex Sans', sans-serif;
        font-weight: 600;
        font-size: 32px;
        line-height: 130%;
        color: $light-gray;
        transition: color 0.2s ease-in-out;

        @include small {
          font-size: 20px;
        }
      }

      .invisible {
        flex-shrink: 10;
        align-self: stretch;
        width: 100%;
        border-left: 1px solid rgba(40, 41, 41, 0.2);
        border-right: 1px solid rgba(40, 41, 41, 0.2);

        @include sm {
          border-left: none;
        }
      }

      > div:not(.invisible) {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 16px;
        min-width: 46px;
        min-height: 46px;
        border: 1px solid #FFFFFF;
        border-radius: 100%;
        background: #0D0D0D;
        transition: border 0.2s ease-in-out;

        svg {
          transform: rotate(45deg);
          transition: transform 0.2s ease-in-out;
        }
      }

      &:hover, &_opened {
        background: rgba(8, 8, 8, 0);

        h2 {
          color: #0D0D0D;
        }

      }

      &_opened {
        > div:not(.invisible) {
          border: 1px solid #0D0D0D;

          svg {
            transform: rotate(-45deg);
          }
        }
      }
    }
  }

  // Hidden content
  &__content-wrap {
    overflow: hidden;
    transition: height 0.3s ease-in-out;

    .accordion-content {
      font-family: 'IBM Plex Sans', sans-serif;
      line-height: 150%;
      color: #FFFFFF;
      padding: 29px 28px 33px 40px;
      justify-content: space-between;

      @include lgMiddle {
        padding: 16px 15px 48px 15px;
      }
      @include lg {
        flex-direction: column;
      }

      &__left {
        font-size: 16px;
        display: flex;
        align-items: center;
        @include small {
          font-size: 15px;
        }
      }

      &__right {
        padding-left: 40px;

        @include lg {
          padding: 24px 0 0 0;
          margin-top: 24px;
          border-top: 1px solid rgba(40, 41, 41, 0.2);
        }

        h3 {
          font-weight: 600;
          font-size: 28px;
          line-height: 130%;
          color: #fff;
          margin-bottom: 40px;

          @include small {
            font-size: 20px;
          }
        }

        ul {
          display: flex;
          width: 677px;
          flex-wrap: wrap;
          row-gap: 30px;
          column-gap: 54px;

          @include sm {
            width: 100%;
          }
        }
      }
    }
  }
}