@import "src/variables/variables";

:root {
  --button-background: #FCEF9A;
  --button-color: #16171C;
}

.project-section {
  position: relative;
  font-family: 'Onest', sans-serif;
  padding: 0;

  &:nth-child(n+2) {
    margin-top: 8px;
  }

  @include xl {
    padding: 0 16px;
  }

  &_reverse {
    .container {
      flex-direction: row-reverse;
    }
  }

  > img {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  .container {
    border-left: 1px solid rgba(255, 255, 255, 0.1);
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    display: flex;
    gap: 24px;
    position: relative;
    z-index: 1;

    @include media(800px) {
      flex-direction: column;
    }

    > div {
      flex: 1;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 27px 24px 48px 48px;
    min-height: 520px;

    @include media(800px) {
      min-height: auto;
    }
    @include small {
      padding: 27px 16px 16px 16px;
    }

    img {
      max-width: 100%;
    }

    p {
      font-size: 22px;
      line-height: 150%;
      color: #FFFFFF;
      margin: 47px 0 40px;

      @include small {
        font-size: 14px;
        margin: 24px 0;
      }
    }
  }

  &__image-wrap {
    display: flex;
    align-items: center;
    justify-content: center;

    &_presidents {
      justify-content: flex-end;
    }

    img {
      width: 100%;
      max-width: 477px;
      height: auto;
    }
  }

  &__button {
    display: block;
    text-align: center;

    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 22px;
    line-height: 29px;
    text-transform: uppercase;
    padding: 20px;
    width: max-content;
    transition: color 0.2s ease-in-out, background 0.2s ease-in-out, border 0.2s ease-in-out;

    min-width: 315px;
    color: var(--button-color);
    background: var(--button-background);
    border: 2px solid var(--button-background);

    @include media(800px) {
      margin: 0 auto;
    }
    @include small {
      font-size: 16px;
      line-height: 21px;
      width: 100%;
      min-width: auto;
    }

    &:hover {
      color: var(--button-background);
      background: transparent;
      border: 2px solid var(--button-background);

      svg {
        path {
          stroke: var(--button-background);
        }
      }
    }

    svg {
      margin-left: 10px;

      path {
        stroke: var(--button-color);
        transition: stroke 0.2s ease-in-out;
      }
    }
  }
}