.block-with-icon {
  padding: 24px 24px 38px;
  font-family: 'IBM Plex Sans', sans-serif;
  line-height: 150%;

  &__header-wrap {
    display: flex;
    align-items: center;

    > h3 {
      padding-left: 16px;
      font-weight: 600;
      font-size: 22px;
      line-height: 130%;
      color: #000000;
    }
  }

  &__img-wrap {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    > img {
      width: 40px;
      height: 40px;
    }

    > div {
      position: absolute;
      font-weight: 500;
      font-size: 22px;
      color: #000000;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  > h3 {
    font-weight: 600;
    font-size: 22px;
    line-height: 130%;
    margin-top: 16px;
  }

  p {
    margin-top: 16px;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 16px;
    color: #282929;
  }
}