@import "src/variables/variables";

.functions {

  &__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    @include lg {
      align-items: center;
      flex-direction: column;
    }
  }

 h2 {
    width: 790px;
    text-align: center;
    color: $light-gray;
    margin: 0 auto 32px;

    @include lg {
      margin-bottom: 24px;
      width: 100%;
      padding: 0 16px;
      font-size: 24px;
      line-height: 37px;
    }
  }
}

.function {
  padding: 24px 24px 20px;
  width: 380px;
  background: #0D0D0D;
  border: 1px solid rgba(255, 255, 255, 0.2);
  text-align: left;
  margin-bottom: 20px;

  &:not(:last-child) {
    margin-right: 20px;
    @include lg {
      margin-right: 0;
    }
  }

  @include lg {
    margin-bottom: 20px;
    flex-direction: column;
  }
  @include mb {
    width: 330px;
    padding: 24px 16px;
  }

  &__img {
    margin-bottom: 24px;
    width: 332px;
    height: 170px;
    @include mb {
      width: 298px;
      height: 160px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 130%;
    margin-bottom: 4px;
    color: $light-gray;

    @include mb {
      font-size: 30px;
    }
  }

  &__informations {
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 135%;
    color: rgba(255, 255, 255, 0.3);
  }
}