@import "src/variables/variables";

.scroll-items {
  padding: 0;

  h2 {
    color: #F6F9F6;
    padding: 0 16px;
  }

  &__list-wrap {
    width: 100%;
    overflow: hidden;

    > div {
      &:first-child {
        margin-bottom: 24px;
        position: relative;
      }
    }
  }

  &__track {
    display: flex;
    gap: 24px;

    &:first-child {
      justify-content: flex-end;
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 18px;
    line-height: 180%;
    color: #F6F9F6;
    min-width: 252px;
    height: 60px;
    border: 1px solid rgba(246, 249, 246, 0.2);

    @include sm {
      font-weight: 300;
      font-size: 13px;
      line-height: 17px;
      min-width: 160px;
      height: 48px;

      &:after, &:before {
        position: absolute;
        content: '';
        background: rgba(255, 255, 255, 0.2);
        bottom: 0;
        width: 1px;
        height: 100%;
      }

      &:after {
        right: 10px;
      }

      &:before {
        left: 10px;
      }
    }
  }
}