@import "src/variables/variables";

.what-are-main {
  &__items {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);

    @include lg {
      grid-template-columns: repeat(2, 1fr);
    }
    @include small {
      grid-template-columns: repeat(1, 1fr);
      gap: 8px;
    }

    li {
      font-family: 'IBM Plex Sans', sans-serif;
      font-size: 16px;
      line-height: 180%;
      padding: 24px 24px 36px;
      border: 1px solid rgba(40, 41, 41, 0.2);

      &:nth-child(4) {
        grid-column-start: 1;
        grid-column-end: 3;

        @include lg {
          grid-row-start: 2;
        }
        @include small {
          grid-column-end: auto;
          grid-row-start: auto;
        }
      }

      h3 {
        font-size: 28px;
        line-height: 130%;
      }

      p {
        color: #282929;
        margin-top: 8px;
      }
    }
  }
}