@import "src/variables/variables";

.work-with-influencers {
  &__grid {
    display: grid;
    gap: 1px;
    grid-template-columns: repeat(3, 1fr);
    border: 1px solid rgba(40, 41, 41, 0.2);
    background: rgba(40, 41, 41, 0.2);

    @include sm {
      grid-template-columns: repeat(2,1fr);
    }

    @include small {
      grid-template-columns: repeat(1,1fr);
    }

    > div {
      background: #FAFAFA;


      &:first-child {
        background: #000000;

        @include sm {
          grid-column-start: 1;
          grid-column-end: 3;
        }
        @include small {
          grid-column-end: auto;
        }

        div, h3 {
          color: #F5F5F5;
        }
      }

      &:nth-child(2) {
        grid-column-start: 2;
        grid-column-end: 4;

        @include sm {
          grid-column-start: 1;
          grid-column-end: 3;
        }
        @include small {
          grid-column-end: auto;
        }
      }
      &:nth-child(5) {

        @include sm {
          grid-column-start: 1;
          grid-column-end: 3;
        }
        @include small {
          grid-column-end: auto;
        }
      }
    }
  }
}